

// 0-1 values of each "effect" slider
import {TYPE_TO_NAME} from './message-constants.js';
        
export const parseMessage = (e, uniforms) => {
    let name = TYPE_TO_NAME[e.type];
    if (name && uniforms.current[name]) {
        uniforms.current[name].value = parseFloat(e.body);
        return true;
    }
    return false;
};
