import React, {useCallback, useRef} from 'react';
import { useSigner } from 'wagmi';
import { ethers } from "ethers";
import mainnetZoraAddresses from "@zoralabs/v3/dist/addresses/1.json"; // Mainnet addresses, 4.json would be Rinkeby Testnet 
import { AsksV11__factory } from "@zoralabs/v3/dist/typechain/factories/AsksV11__factory";

export const useBuy = (contract="0x7269c1a1b8aba1390770193771f92bd722c6a77b") => {
    const { data: signer, isError, isLoading } = useSigner();
    let signerRef =  useRef(signer);
    signerRef.current = signer;

    /*
      * uncomment to test on rinkeby
    contract = "0xe81f2369e74debd9874466d3e31aecf700aed872";
    */

    const buy = useCallback(async (tokenId, fillAmount) => {
        try {
            /*
             * Uncomment to test on rinkeybey
            const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
            // Prompt user for account connections
            let accounts = await provider.send("eth_requestAccounts", []);
            const signer = provider.getSigner();

            console.log("signer accounts=", signer, accounts);

            fillAmount = undefined;
            tokenId = 3;
            */

            tokenId = tokenId.toString();
            const askModuleContract = AsksV11__factory.connect(mainnetZoraAddresses.AsksV1_1, signerRef.current);
            let ask = await askModuleContract.askForNFT(contract, tokenId);
            fillAmount = fillAmount ? ethers.parseEth(fillAmount.toString()) : ask.askPrice.toString();
            const finder = "0x0000000000000000000000000000000000000000"; // 0 address for finder
            
            let tx = await askModuleContract.fillAsk(
                contract,
                tokenId, // Token Id
                "0x0000000000000000000000000000000000000000", // 0 address for ETH sale
                fillAmount,
                finder,
                {
                    value: fillAmount
                }
            );
            return tx;
        } catch (e) {
            console.log("error = ", e);
            return false;
        }
    }, [contract, signer]);

    return buy;
};
