export const FILTER = `
vec4 filterColor(vec4 color) {
   vec2 uv = gl_FragCoord.xy / vec2(width*2.0, height*2.0);
   float val = filterSliderValue;
   vec4 sampled = texture2D(video, uv - vec2(0.1*pow(val, 2.0), 0.1*filterSliderValue));
   sampled.g = 0.0;
   sampled.b = 0.0;
   float ival = 1.0-val;
   vec4 lp = mix(color, vec4(color.x*ival, color.y*pow(ival, 3.0), color.z*pow(ival+0.1, 0.5), 1.0), smoothstep(0.5, 1.0, val));
   return mix(mix(color, sampled, pow(val, 0.03)), lp, pow(val, 0.3));
}

`;
