import {VHS} from './vhs.js';
import {FILTER} from './filter.js';

export const fragmentShader = `

uniform sampler2D video;
uniform float width;
uniform float height;
uniform float gateStrength;
uniform float playRate;
uniform float isRolling;
uniform float ticks;
uniform float rollRate;
uniform float blurring;
uniform float filterSliderValue;
uniform float spaceEffectLevels;
uniform float spaceSliderValue;

vec4 blur(float Size) {
float Pi = 6.28318530718; // Pi*2
    
    // GAUSSIAN BLUR SETTINGS {{{
    float Directions = 16.0; // BLUR DIRECTIONS (Default 16.0 - More is better but slower)
    float Quality = 4.0; // BLUR QUALITY (Default 4.0 - More is better but slower)
    //float Size = 8.0; // BLUR SIZE (Radius)
    // GAUSSIAN BLUR SETTINGS }}}
   
    vec2 Radius = Size/vec2(width, height); //iResolution.xy;
    
    // Normalized pixel coordinates (from 0 to 1)
    vec2 uv = gl_FragCoord.xy / vec2(width*2.0, height*2.0);
    // Pixel colour
    vec4 Color = texture2D(video, uv);
    
    // Blur calculations
    for( float d=0.0; d<Pi; d+=Pi/Directions)
    {
		for(float i=1.0/Quality; i<=1.0; i+=1.0/Quality)
        {
			Color += texture2D(video, uv+vec2(cos(d),sin(d))*Radius*i);		
        }
    }
    
    // Output to screen
    Color /= Quality * Directions - 15.0;

    return Color*0.6;
}
vec4 blur9(sampler2D image, vec2 direction) {
  vec2 uv = gl_FragCoord.xy / vec2(width*2.0, height*2.0);
  vec2 resolution =  vec2(width, height); 
  vec4 color = vec4(0.0);
  vec2 off1 = vec2(1.411764705882353) * direction;
  vec2 off2 = vec2(3.2941176470588234) * direction;
  vec2 off3 = vec2(5.176470588235294) * direction;
  color += texture2D(image, uv) * 0.1964825501511404;
  color += texture2D(image, uv + (off1 / resolution)) * 0.2969069646728344;
  color += texture2D(image, uv - (off1 / resolution)) * 0.2969069646728344;
  color += texture2D(image, uv + (off2 / resolution)) * 0.09447039785044732;
  color += texture2D(image, uv - (off2 / resolution)) * 0.09447039785044732;
  color += texture2D(image, uv + (off3 / resolution)) * 0.010381362401148057;
  color += texture2D(image, uv - (off3 / resolution)) * 0.010381362401148057;
  return color;
}

// A single iteration of Bob Jenkins' One-At-A-Time hashing algorithm.
uint hash( uint x ) {
    x += ( x << 10u );
    x ^= ( x >>  6u );
    x += ( x <<  3u );
    x ^= ( x >> 11u );
    x += ( x << 15u );
    return x;
}

// Compound versions of the hashing algorithm I whipped together.
uint hash( uvec2 v ) { return hash( v.x ^ hash(v.y)                         ); }
uint hash( uvec3 v ) { return hash( v.x ^ hash(v.y) ^ hash(v.z)             ); }
uint hash( uvec4 v ) { return hash( v.x ^ hash(v.y) ^ hash(v.z) ^ hash(v.w) ); }


// Construct a float with half-open range [0:1] using low 23 bits.
// All zeroes yields 0.0, all ones yields the next smallest representable value below 1.0.
float floatConstruct( uint m ) {
    const uint ieeeMantissa = 0x007FFFFFu; // binary32 mantissa bitmask
    const uint ieeeOne      = 0x3F800000u; // 1.0 in IEEE binary32

    m &= ieeeMantissa;                     // Keep only mantissa bits (fractional part)
    m |= ieeeOne;                          // Add fractional part to 1.0

    float  f = uintBitsToFloat( m );       // Range [1:2]
    return f - 1.0;                        // Range [0:1]
}



// Pseudo-random value in half-open range [0:1].
float rand( float x ) { return floatConstruct(hash(floatBitsToUint(x))); }
float rand( vec2  v ) { return floatConstruct(hash(floatBitsToUint(v))); }
float rand( vec3  v ) { return floatConstruct(hash(floatBitsToUint(v))); }
float rand( vec4  v ) { return floatConstruct(hash(floatBitsToUint(v))); }


${VHS}

${FILTER}


void main() {
  vec2 uv = gl_FragCoord.xy / vec2(width*2.0, height*2.0);
  vec4 blurred = blurring > 0.0 ? blur9(video, vec2(blurring*3.0, blurring*1.0)) : vec4(0.0);

/*
  vec4 blurred = mix(
    blur(blurring*4.0), 
    
*/
  vec4 sampled = texture2D(video, uv);
  float _gate = (1.0-gateStrength);

  vec4 glitched = spaceSliderValue > 0.0 || isRolling == 1.0 ? glitch() : vec4(0.0);
  vec3 gammaColor = mix(
    mix(
      sampled.xyz, 
      glitched.xyz, 
      isRolling), 
   blurred.xyz, pow(blurring, 0.3));
        
  vec3 color = pow(gammaColor, vec3(2.0));
  // grayscale conversion
  float gray = dot(color, vec3(0.2126, 0.7152, 0.0722));
  // regamma
  float gammaGray = sqrt(gray);
  vec4 greyImage = vec4(gammaGray, gammaGray, gammaGray, 1.0);
  vec4 _color = 
    _gate *
    mix(
      greyImage, 
      mix(
        mix(
          sampled, 
          glitched, 
          isRolling + spaceSliderValue*(0.3+spaceEffectLevels)), 
        blurred, 
        pow(blurring, 0.3)
      ), 
      pow(playRate, 4.0));
  _color.w = 1.0;
  gl_FragColor = filterSliderValue > 0.0 ? filterColor(_color) : _color;
}
`;
