import React, { useEffect, useCallback, useState} from 'react';
import {useVideoSize} from './useVideoSize.js';
import {isMobile} from 'react-device-detect';
import {Playbar} from 'components/player/Playbar.js';
import {connect} from 'react-redux';
const DURATION = 232;

const PlaybarContainer = ({videoRef, playerRef, started}) => { 
    let {width, height} = useVideoSize(videoRef);
    let [progress, setProgress] = useState(0);

    useEffect(() => {
        window.addEventListener("message", onMessage);
        return () => window.removeEventListener("message", onMessage);
    } , [setProgress]);

    const onMessage= useCallback((e) => {
        if (e.data.type === "song-player-progress") {
            let progress = parseFloat(e.data.body);
            let x = progress/100
            let sec = DURATION * x;
            setProgress(sec);
        }
    }, [setProgress]);

    return (
        <div 
          className="z-50 absolute top-1/2  md:h-full md:w-4/6 md:transform md:translate-y-1/3 w-full"
          style={isMobile ? {top: height} : {width: width}}
        >
          <Playbar playerRef={playerRef}
                   color="white"/>
          {started &&
           <div className="absolute md:left-0 left-4 top-3 text-xs">
            {format(progress)}
           </div>}
          {started &&
           <div className="absolute md:right-0 right-4 top-3 text-xs">
             -{format(Math.abs(progress-DURATION))}
           </div>}
        </div>
    );
}

function format(duration)
{   
    // Hours, minutes and seconds
    var hrs = ~~(duration / 3600);
    var mins = ~~((duration % 3600) / 60);
    var secs = ~~duration % 60;

    // Output like "1:01" or "4:03:59" or "123:03:59"
    var ret = "";

    if (hrs > 0) {
        ret += "" + hrs + ":" + (mins < 10 ? "0" : "");
    }

    ret += "" + mins + ":" + (secs < 10 ? "0" : "");
    ret += "" + secs;
    return ret;
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PlaybarContainer);
