import React, { useEffect, useCallback, useRef, useState} from 'react';
import {connect} from 'react-redux';

const ZorbOption = ({setZorbId, zorbId, zorb, setZorbStartPosition}) => { 
    let ref = useRef();

    let selected = zorbId === parseInt(zorb.tokenId);
    useEffect(() => {
    }, []);

    useEffect(() => {
        if (selected) {
            let rect = ref.current.getBoundingClientRect();
            setZorbStartPosition({x: rect.left, y: rect.top, image: zorb.image.url});
        }
    }, [selected, setZorbStartPosition]);

    return (
        <div
          ref={ref}
          onClick={() => setZorbId(parseInt(zorb.tokenId))}
          className={"pushable zorb-option " + (selected ? "selected" : " unselected")}>
          <img src={zorb.image.url}  
               className={(selected ? "selected" : " unselected")}/>
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbOption);
