import React, { useEffect, useCallback, useState} from 'react';
import {useTokensOnSale} from '../hooks/useTokensOnSale.js';
import {connect} from 'react-redux';
import FinalizePurchase from './FinalizePurchase.js';
import { useAccount } from 'wagmi';

const BuyPlayerFLow = ({name, setBuying, thumbnail, askPrice, setCompletedBuy, tokenId}) => { 
    const {data, isError, isLoading } = useAccount();
    console.log("acc data =", data);
    let {setTokensOnSale, tokensOnSale} = useTokensOnSale();

    if (!tokensOnSale || tokensOnSale.length === 0) {
        return '';
    }
    return (
        <FinalizePurchase
          name={name}
          _askPrice={askPrice}
          _thumbnail={thumbnail}
          account={data && data.address}
          onCompletedBuy={() => setCompletedBuy(true)}
          onDone={() => {
              setBuying(false);
              
          }}
          tokensOnSale={tokensOnSale} onCancel={() => setBuying(false)} cart={[tokenId]} setCart={() => 0}/>
    );
};


const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(BuyPlayerFLow);
