import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './playbar.scss';

export const Playbar = ({color, playerRef}) => { 
    let ref = useRef();
    let scrubRef = useRef();
    let [waveform, setWaveform] = useState();
    let [progress, setProgress] = useState(0);
    let [playing, setPlaying] = useState(0);
    let [started, setStarted] = useState(false);
    let [scrubbing, setScrubbing] = useState(false);
    let off = useRef(0);
    let [scrub, setScrub] = useState(undefined);
    

    useEffect(() => {
        window.addEventListener("mouseup", finishScrub);
        window.addEventListener("mousemove", onMouseMove);
        window.addEventListener("touchmove", onMouseMove);
        return () => {
            window.removeEventListener("mouseup", finishScrub);
            window.removeEventListener("mousemove", onMouseMove);
            window.removeEventListener("touchmove", onMouseMove);
        }
    }, [scrubbing, setScrub, setScrubbing]);

    const finishScrub = useCallback(() => {
        setScrubbing(false);
    }, [setScrubbing]);

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
        return x => window.removeEventListener("message", onMessage, false);
    }, [setProgress, setPlaying, setStarted, setWaveform]);

    const onMessage = useCallback(e => {
        if (e.data.type === "waveform-data") {
            setWaveform(e.data.body);
        }
        if (e.data.type === "song-player-progress") {
            let progress = parseFloat(e.data.body);
            setProgress(progress);
        }
        if (e.data.type === "jacker-playback-status") {
            let playing = parseInt(e.data.body);
            setPlaying(playing);
            if (playing) {
                setStarted(true);
            }
        }
    }, [started, setProgress, setPlaying, setStarted, setWaveform]);

    const onPlayClicked = useCallback(() => {
        playerRef.current.contentWindow.postMessage({
            type: "jacker-pause-toggle",
            body: true,
        }, "*");
    }, []);

    const onMouseMove = useCallback((e) => {
        if (!scrubbing) {
            return;
        }
        if (e.touches) {
            e = e.touches[0];
        }

        let rect = ref.current.getBoundingClientRect();
        let x = e.clientX - rect.left;
        let percent = (x / rect.width) * 100;
        percent = Math.max(0, Math.min(100, percent));
        setScrub(percent);
        playerRef.current.contentWindow.postMessage({
            type: "jacker-seek",
            body: percent
        }, "*");
    }, [scrubbing, setScrub]);

    let className = "shuffle-buttons";

    if (!started) {
        return '';
    }

    let style = {
        borderColor: color
    };

    progress = scrubbing && scrub ? scrub : progress;
    return (
          <div
            onMouseDown={(e) => {
                setScrubbing(true);
                onMouseMove(e);
            }}
            onTouchStart={(e) => {
                setScrubbing(true);
                onMouseMove(e);
            }}
            ref={ref}
            style={style} className="relative h-1.5 rounded-lg w-full playbar">
            <div
              style={{
                  width: (100-progress) + "%",
                  left: progress + "%"}} className="w-full h-full rounded-lg absolute playbar-remaining"/>
            <div
              ref={scrubRef}
              onMouseDown={(e) => {
                  e.stopPropagation();
                  setScrubbing(true);
              }}
              onTouchStart={(e) => {
                  e.stopPropagation();
                  setScrubbing(true);
              }}
              style={{
                  //backgroundColor: color,
                  left: progress + "%"}} className="rounded-xl absolute z-50 w-3 h-3 -top-0.5 bg-white hover:bg-red-500"/>
          </div>
    );
};
