
import ipfs from 'decentralized/Ipfs.js';
export const save = async (json) => {
    console.log("saving json=", json);
    let hash = await ipfs.addJson(json);
    // save the hash and name

    let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/saveJack/" : "https://zequencer.io/saveJack/";
    if (!window.LOCAL_BACKEND_FLAG) {
        baseURL = "https://zequencer.io/saveJack/";
    }
    fetch(baseURL, {
        method: "POST",
         headers: {
             'Accept': 'application/json',
             'Content-Type': 'application/json'
         },
        body: JSON.stringify({
            data: hash,
            title: json.name,
            jack: json
        })
    });
};

export const saveWaitlist = async (address, message) => {
    let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/sporeWaitlist/" : "https://zequencer.io/sporeWaitlist/";
    if (!window.LOCAL_BACKEND_FLAG) {
        baseURL = "https://zequencer.io/sporeWaitlist/";
    }
    fetch(baseURL, {
        method: "POST",
         headers: {
             'Accept': 'application/json',
             'Content-Type': 'application/json'
         },
        body: JSON.stringify({
            address,
            message
        })
    });
};
