import React, {useEffect, useCallback, useState} from 'react';
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import {connect} from 'react-redux';
import { IoLogoTwitter } from 'react-icons/io';
import { BiInfoCircle } from 'react-icons/bi';
import "./navigation.scss";

const Navigation = ({clear, jacked, setShowInfo}) => {
    const [visible, setVisibility] = useState(false);
    const clicked = () => {
        setVisibility(visible ? false : true);
    };

    const hide = () => {
        setVisibility(false);
    };

    return (
        <>
            <div className="menuAction pushable" onClick={clicked}>
                <img src={jacked != undefined ? "/spores-logo-black.svg" : "/spores-logo-white.svg"}></img>
            </div>
            <div className="navFullContainer" style={{display: visible ? "inherit" : "none"}} onClick={hide}>
            <div className="dropMenu" style={{display: visible ? "inherit" : "none"}}>
              <div className="connect-button-wrapper">
                <ConnectButton
                  accountStatus="address" 
                  showBalance={{ smallScreen: false, largeScreen: false }}
                  label="Connect Wallet"
                />
              </div>
                <a href="/" className="navDropItem largeItem play">Play</a>
                <a href="/buy" className="navDropItem largeItem buy">Buy</a>
                <a href="/learn" className="navDropItem largeItem learn">Learn</a>
                <div className='iconContainer'>
                    {jacked != undefined && <div className="navItem smallItem pushable" style={{marginTop: "-2px", color: "black"}} onClick={() => setShowInfo(true)}><BiInfoCircle/></div>}
                    <div className="navItem smallItem pushable" style={{marginTop: "-2px"}}><a href="https://twitter.com/SporesDAO"><IoLogoTwitter/></a></div>
                    <div className="navItem smallItem pushable" style={{marginTop: "-2px"}}><a href="/lore"><img style={{height: "20px"}} src='./mushroom-icon.svg'></img></a></div>
                </div>
            </div>
        </div>
        </>
    )
}

const mapStateToProps = (state, props) => {
    return {
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
    };
  };
  
  export default connect(mapStateToProps, mapDispatchToProps)(Navigation);
