import {useRef, useState, useCallback, useEffect} from 'react';
import {parseMessage} from './message-parser.js';
const DURATION = 232;

const MAX_ACCEPTABLE_DRIFT = 0.2; // 200 ms of maximum dift
const DRIFT_COOLDOWN_PERIOD = 3000; // 1500 mscooldown between drift corrects
export const useVideoController = ({ref, playerRef}) => {
    let baseBPM = useRef();
    let lastTime = useRef(0);
    let nextTick = useRef(false);
    let lastVideoTime = useRef(0);
    let lastPlaybackRate = useRef(0);
    let counter = useRef(0);
    let waiting = useRef();
    let timer = useRef(0);
    let gateTimer = useRef(undefined);
    let coolDownStartTime = useRef(undefined);

    const isCoolingDown = () => {
        return coolDownStartTime.current && ((new Date().getTime() - coolDownStartTime.current) <= DRIFT_COOLDOWN_PERIOD);
    };

    const startCoolDown = (time) => {
        coolDownStartTime.current = new Date().getTime();
    };


    let blurDirection = useRef(.1);

    let uniforms = useRef({
        blurring: {
            value: 0.0,
        },
        playRate: {
            value: 1.0
        },
        isRolling: {
            value: 0.0,
        },
        rollRate: {
            value: 32.0,
        },
        filterSliderValue: {
            value: 0.0
        },
        spaceSliderValue: {
            value: 0.0
        },
        spaceEffectLevels: {
            value: 0.0
        },
        ticks: {
            value: 0
        },
        gateStrength: {
            value: 0,
        }
    });
    let [isPlaying, setIsPlaying] = useState(false);
    let [started, setStarted] = useState(false);
    
    useEffect(() => {
        window.addEventListener("message", onMessage, false);
    }, [setIsPlaying, setStarted]);

    useEffect(() => {
        setInterval(() => {
            blurLoop();
        }, 25);
    }, []);
    
    const blurLoop = () => {
        if (uniforms.current.blurring.value > 0.0) {
            let blurring = uniforms.current.blurring.value;
            blurring = Math.max(0, Math.min(2.0, blurring + blurDirection.current));
            if (blurring >= 2 || blurring <= 0) {
                if (blurDirection.current < 0) {
                    blurDirection.current = 0.1;
                } else {
                    blurDirection.current = -0.00005;
                }
            } 
            uniforms.current.blurring.value = blurring;
            if (blurDirection.current > 0) {
                blurDirection.current *= 1.8;
            } else {
                blurDirection.current *= 1.6;
                blurDirection.current = Math.max(-.15, blurDirection.current);
            }
        };
    };
        
    const fastForward = useCallback(() => {
        let rate = ref.current.playbackRate;
        let nextTime = Math.min(DURATION, ref.current.currentTime + 10/rate);
        let percent = 100*nextTime / DURATION;

        playerRef.current.contentWindow.postMessage({
            type: "jacker-seek",
            body: percent
        }, "*");
        playerRef.current.currentTime = nextTime;
    }, []);

    const rewind = useCallback(() => {
        let rate = ref.current.playbackRate;
        let nextTime = Math.max(0, ref.current.currentTime - 10/rate);
        let percent = 100*nextTime / DURATION;

        playerRef.current.contentWindow.postMessage({
            type: "jacker-seek",
            body: percent
        }, "*");
        playerRef.current.currentTime = nextTime;
    }, []);

    const onMessage = useCallback((e) => {
        if (parseMessage(e.data, uniforms)) {
            // we already consumed them into uniforms
            return;
        }

        if (e.data.type === "jacker-playback-status") {
            setStarted(true);
            let playing = parseInt(e.data.body);
            if (playing) {
                ref.current.play();
            } else {
                ref.current.pause();
            }
            setIsPlaying(playing);
        }
        if (e.data.type === "song-player-progress") {
            uniforms.current.ticks.value++;

            let progress = parseFloat(e.data.body);
            let x = progress/100
            let sec = DURATION * x;
            let drift = sec - ref.current.currentTime;
            
            let isDrifting = (Math.abs(drift) > MAX_ACCEPTABLE_DRIFT) && !isCoolingDown();
            let remainingCondition = sec <= lastTime.current || sec >= lastTime.current+1.0;
            let tickTime = ref.current.currentTime - lastVideoTime.current;
            if (tickTime < 0.1) 
                /*
                console.warn("drift=%s tickTime=%s", drift, tickTime);
            } else {
                console.log("drift=%s tickTime=%s", drift, tickTime);
            }
            */
            lastVideoTime.current = ref.current.currentTime;
            if (Math.abs(drift) > 0.5) {
                isDrifting = true;
            }
            if (isDrifting) {
                startCoolDown();
            }
            if (isDrifting || sec <= lastTime.current || sec >= lastTime.current+1.0) {
                let _drift = drift*1000;
                if (ref.current.fastSeek &&
                    uniforms.current.isRolling.value && uniforms.current.rollRate.value === 32) {
                    // we want to use fast seek when 1/32th rolling
                    ref.current.fastSeek(sec);
                } else {
                    ref.current.currentTime = sec;
                }
                nextTick.current = false;
            }
            lastTime.current = sec;
            if (gateTimer.current !== undefined) {
                gateTimer.current++;
                if (gateTimer.current > 1) {
                    gateTimer.current = undefined;
                }
            }

        }
        if (e.data.type === "song-bpm") {
            let bpm = e.data.body;
            baseBPM.current = parseFloat(bpm);
            
        }

        if (e.data.type === "playback-bpm") {
            let bpm = e.data.body;
            if (baseBPM.current) {
                if (uniforms.current.blurring.value == 0) {
                    uniforms.current.blurring.value = 0.1; //(uniforms.current.blurring.value + 0.2)/2;
                    blurDirection.current = 0.1;
                }
                blurDirection.current *= 0.92;
                let playbackRate = bpm / baseBPM.current;
                let time = ref.current.currentTime;
                uniforms.current.playRate.value = playbackRate;
                ref.current.playbackRate = playbackRate;
                if (!isCoolingDown()) {
                    startCoolDown();
                }
            }
        }
    }, [setIsPlaying, setStarted]);

    return {uniforms, isPlaying, started, rewind, fastForward};
};
