import React, {useEffect, useState} from 'react';
import {connect} from 'react-redux';
import PulseLoader from 'react-spinners/PulseLoader'
import './progressbar.scss';
const WIDTH = 390;

const UnknownProgressBar = ({
    text,
    className="",
    width=WIDTH,
    intervalTime=100,
    intervalPiece=10}) => {
    let [loadingProgress, setLoadingProgress] = useState(24);

    useEffect(() => {
        const interval = setInterval(() => {
            setLoadingProgress(x => (x + intervalPiece) % 100);
        }, intervalTime);
        return () => clearInterval(interval);
    }, []);
    
    let style = {
        width: width * loadingProgress / 100.0
    };
    return (
      <div className={"progress-bar" + className}>
        <div style={{
          borderRadius: "35px",
          height: "70px",
          width: "70px",
          position: "absolute",
          margin: "auto",
          left: "0",
          right: "0",
          top: "0",
          bottom: "0"}}>
          <img style={{width: "100%"}} src='/spores-icon-glow.svg'></img>
        </div>
      </div>
        // <div className={"progress-bar " + className}>
        //   <div className="inner-progress-bar">
        //   <div className="progress" style={style}/>
        //     <div className="loading-text">
        //       {text} ...
        //     </div>
        //   </div>
        // </div>
    );
};

const mapStateToProps = (state, props)=> {
    return {
    };
};


export default connect(mapStateToProps, null)(UnknownProgressBar);
