
import { ConnectButton, darkTheme } from '@rainbow-me/rainbowkit';
import React, { useEffect, useCallback, useState} from 'react';
import PulseLoader from 'react-spinners/PulseLoader'
import {connect} from 'react-redux';
import './finalize.scss';
import {useMintTest} from './test.js';
import {useBuy} from 'components/hooks/useBuy.js';
import {useBalance} from 'wagmi';
import {useSigner} from 'wagmi';

const FinalizePurchase = ({account, _askPrice, _thumbnail, name, tokensOnSale, onCompletedBuy, onCancel, cart, onDone, contractAddress}) => { 
    let balanceData = useBalance({addressOrName: account});
    let {data} = useSigner();
    let balance = balanceData.data && parseFloat(balanceData.data.formatted);
    let [finished, setFinished] = useState(false);
    let [error, setError] = useState();
    let [current, setCurrent] = useState(cart[0]);
    let [waitingForWallet, setWaitingForWallet] = useState(false);
    let [confirming, setConfirming] = useState(false);

    const buy = useBuy(contractAddress);
   
    let thumbnail = _thumbnail || tokensOnSale.find(x => x.tokenId === current).thumbnail;
    let number = cart.indexOf(current) + 1;
    let total = cart.length;

    let obj = tokensOnSale && tokensOnSale.find(x => x.tokenId === current);
    let askPrice = _askPrice || obj.askPrice;

    const completeBuy = useCallback(() => {
        console.log('complete buy called');
        let tokenId = current;
        // we wanna buy this one
        setWaitingForWallet(true);
        buy(tokenId).then(
            x => {
                if (x) {
                    setConfirming(x.hash);
                    x.wait(1).then(x => {
                        setFinished(true);
                        setConfirming(false);
                        if (number === cart.length) {
                            // we have truly completed
                        } else {
                            setCurrent(cart[number]);
                        }
                        onCompletedBuy(tokenId);
                    });
                    setWaitingForWallet(false);
                    setError(undefined);
                } else {
                    setError("You rejected the transaction. Please try again or continue browsing.");
                }
                setWaitingForWallet(false);
            });
    }, [current, setCurrent, number, setWaitingForWallet, cart]);

    if (finished || confirming) {
        return (
        <div className="finalize-modal-drop">
          <video
            playsInline
            muted
            className={confirming ? "confirming shrooms-video" : "shrooms-video"}
            autoPlay
            loop
            src={"/mushrooms-medium.mp4"}/>
          <div
            onClick={(e) => e.stopPropagation()}
            className="finalize-modal">
            <div onClick={onDone} className="close-button">x</div>
            <div className="logo-container">
              <img className="spores-logo-black" src="/spores-logo-black.svg"/>
            </div>
            <div className="sp-modal-title">
              <div className="sp-title">
                {confirming ? "Completing Purchase" : (name ? "You`ve Bought the " + name : "You\'ve Bought the SP-" + current)}
                
              </div>
            </div>
            {confirming ? <div className="sp-modal-body">
              <p>
                Your new Spore Player should arrive in your wallet in a few moments.
              </p>
                            <div className="pulse-container">
              <PulseLoader
                sizeUnit={"px"}
                size={10}
                color={'#ffffff'}
                loading={true}
              />
                            </div>
                          </div> :
             <div className="sp-modal-body">
               <p>
                 Congrats, you can now dose yourself with some cyber psilocybin.
               </p>
             {!name && <div
                 onClick={() => window.open("/sp/" + current, '_newtab')}
                 className="pushable open-your-player">
                 Open your New Player
                       </div>}
             </div>}
            </div>
        </div>
        );
    }

    return (
        <div onClick={onCancel} className="finalize-modal-drop">
          <div
            onClick={(e) => e.stopPropagation()}
            className="finalize-modal">
            <div onClick={onCancel} className="close-button">x</div>
            <div className="logo-container">
              <img className="spores-logo-black" src="/spores-logo-black.svg"/>
            </div>
            <div className="sp-modal-title">
              <div className="sp-title">
                Buy {name ? name : `SP-${current}`}
              </div>
              {cart.length > 1 && <div className="purchase-part"><span className="part-number">{number}</span> of <span className="total-parts">{total}</span></div>}
            </div>
            <div className="sp-modal-body">
              <p>
                 You're about to buy this Spore Player for a fixed price.
              </p>
              <div className="thumbnail-and-price">
                <img src={thumbnail}/>
                <div className="token-price">
                  <div className="price-label">
                    PRICE
                  </div>
                  <div className="price-eth">
                    {askPrice} ETH
                  </div>
                </div>
              </div>
              {error && <div className="error-text"><img src="/error-icon.svg"/> {error}</div>}
              {waitingForWallet ?
               <div className="waiting-for-wallet">
                 <span className="inner">
                   <span className="helper-text">
                     Waiting for Wallet 
                   </span>
                   <PulseLoader
                     sizeUnit={"px"}
                     size={10}
                     color={'#ffffff'}
                     loading={true}
                   />
                 </span>
               </div> : balance < parseFloat(askPrice) ?
               <div className="low-balance complete-buy">
                 Insufficient balance for purchase.
               </div> :
               !data ? <div className="connect-wallet-container"><ConnectButton
                         accountStatus="address" 
                         showBalance={{ smallScreen: false, largeScreen: false }}
                         label="Connect Wallet"
                       /> </div>: 
               <div
                 onClick={() => completeBuy()}
                 className="complete-buy">
                 Complete Purchase 
               </div>}
            </div>
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FinalizePurchase);
