import {SET_SHOW_PLAYLIST_MENU} from 'constants/action-types.js';
import {SET_CURRENT_PLAYLIST_SONG} from 'constants/action-types.js';
import {SET_PLAYLIST_LOADED} from 'constants/action-types.js';

export const playlistReducer = (state = {currentSong: "Twisting Faith"}, action) => {
    let newState;
    switch (action.type) {
    case SET_SHOW_PLAYLIST_MENU:
        return {
            ... state,
            showPlaylistMenu: action.payload
        };
    case SET_CURRENT_PLAYLIST_SONG:
        console.log("SETTING CURRENT PLAYLIST SONG=", action.payload);
        return {
            ... state,
            currentSong: action.payload
        };
    case SET_PLAYLIST_LOADED:
        console.log("SETTING PLAYLIST LOADED=", action.payload);
        return {
            ... state,
            playlistLoaded: action.payload
        };
    default:
        return state;
    }
}
 
