import React, { useEffect, useCallback, useState} from 'react';
import {Source} from 'components/nft-jacker/HistoryItemSource.js';
import BuyUnsold from 'components/nft-jacker/BuyUnsold.js';
import BuyThisSkin from 'components/nft-jacker/BuyThisSkin.js';

import {connect} from 'react-redux';

const TokenMetadata = ({
    showBuyThisSkin,
    buyThisSkin,
    artist,
    upperGradInverted,
    lowerGradInverted,
    coverArt,
    songName,
    tokenId,
    currentLink,
    children
}) => { 
  return (
      <div className="artist-name" style={{color: upperGradInverted}}>
        {children}
        {artist}
        <div className="song-name" style={{color: upperGradInverted}}>
          {songName}
        </div>
        {coverArt && <img className={artist === "Keyon Christ" ? "cover-art smaller" : "cover-art"} src={coverArt}/>}
        <div />
        <div className={artist === "Keyon Christ" ? "link-and-buy keyon" : "link-and-buy"}>
          {artist === "Keyon Christ" && <BuyUnsold tokenId={tokenId}/>}
          {currentLink && <a className={artist !== "Keyon Christ" ? "current-link align-right" : "current-link"} style={{color: upperGradInverted}} href={currentLink} target="_blank" rel="noreferrer">{artist !== "Keyon Christ" && <Source link={currentLink} textColor={upperGradInverted} />}</a>}
        </div>
        {showBuyThisSkin && artist !== "Keyon Christ" && <BuyThisSkin upperGradInverted={upperGradInverted} lowerGradInverted={lowerGradInverted} buyThisSkin={buyThisSkin}/>}
      </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TokenMetadata);
