import React, { useRef, useEffect, useCallback, useState} from 'react';
import {isMobile} from 'react-device-detect';
import {connect} from 'react-redux';
import './jolly.scss';
import './heatmap.scss';

const JollyRancherPlayer = ({width, spore, progress, height=110}) => { 
    let ref = useRef();
    let loaded = useRef();

    // TODO: need to wait for the iframe to fully load before sending this
    // likely need a post message listener for "zequencerProjectLoaded"

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
        return () => {
            window.removeEventListener("message", onMessage);
        };
    }, [spore]);

    const onMessage = useCallback((e) => {
        if (e.data === "sporeIFrameLoaded") {
            // then we need to send it over
            sendHeatmapHash();
            loaded.current = true;
        }
    }, [spore]);

    useEffect(() => {
        if (loaded.current) {
            console.log("spore.dataHash =", spore.datahash, spore);
            sendHeatmapHash();
        }
    }, [spore]);

    const sendHeatmapHash = useCallback(() => {
        console.log('sending hash', spore.dataHash);
        if (ref.current && ref.current.contentWindow) {
            if (spore) {
                ref.current.contentWindow.postMessage({
                    type: "heatmapHash",
                    body: spore.dataHash
                }, "*");
            }
        }
    }, [spore]);

    useEffect(() => {
        if (ref.current && ref.current.contentWindow) {
            if (spore) {
                ref.current.contentWindow.postMessage({
                    type: "heatmapHash",
                    body: spore.dataHash
                }, "*");
            }
        }
    }, [spore]);

    useEffect(() => {
        if (ref.current && ref.current.contentWindow) {
            ref.current.contentWindow.postMessage({
                type: "sporePosition",
                body: progress
            }, "*");
        }
    }, [progress]);

    useEffect(() => {
        if (ref.current && ref.current.contentWindow) {
            ref.current.contentWindow.postMessage({
                type: "heatmapSize",
                body: [width, 90]
            }, "*");
        }
    }, [width]);
       
    return (
        <iframe
          ref={ref}
          sandbox="allow-scripts allow-same-origin"
          allowtransparency="true"
          height={height}
          width={width}
          aria-describedby="spore"
          alt="spore.vision"
          //src={"https://zequencer.io/ipfs/QmTh6hkgEGGLpVP5XANvDToWMmLNq1VFXcS4vXnCMjrefC/"}
          //src={"https://zequencer.io/ipfs/QmTp5n1FsKeqEj4u8MGRScVY9BGkkWPiR3AEgqe8gvbTeE/"}
          src={"https://zequencer.mypinata.cloud/ipfs/QmVvhZjnEJCBLs7SK2ED9xjbk7ZzjQQq4DHzfh7PJp9314/"}
          className="spore-heatmap zora-mediaObject css-hnzuw7 zora-mediaObject--mediaLoaded zora-mediaObject--isFullPage"
          style={{borderWidth: "0px"}}/>
        );
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(JollyRancherPlayer);
