import React, {useCallback, useEffect, useState} from 'react';
import { prominent } from 'color.js';
import invert, { RGB, RgbArray, HexColor, BlackWhite } from 'invert-color';

export const useCoverArtColors = ({coverArt}) => {
    let [coverGrad, setCoverGrad] = useState();
    let [buttonTextColor, setButtonTextColor] = useState();
    let [buttonBgColor, setButtonBgColor] = useState();
    let [upperGradInverted, setUpperGradInverted] = useState();
    let [lowerGradInverted, setLowerGradInverted] = useState();
    let [mainColor, setMainColor] = useState();


        // cover art background
    useEffect(() => {
      if (coverArt != null) {
        prominent(coverArt, { amount: 3, group: 50, format: "hex"}).then(color => {
          setCoverGrad("linear-gradient(45deg, " + color[0] + " 0%, " + color[1] + " 50%, " + color[2] + " 100%)");
          let invertedLowerColor = invert(color[0], true)
          let invertedMidColor = invert(color[1], true);
          let invertedUpperColor = invert(color[2], true);
            setMainColor(color[0] === "#000000" ? color[1] : color[0]);
          setButtonTextColor(invert(invertedMidColor, true));
          setButtonBgColor(invertedMidColor);
          setUpperGradInverted(invertedUpperColor);
          setLowerGradInverted(invertedLowerColor)
        })
      }
    }, [coverArt, setMainColor, setButtonTextColor, setButtonBgColor, setCoverGrad, setUpperGradInverted, setLowerGradInverted]);

    const clearColors = useCallback(() => {
        setLowerGradInverted();
        setUpperGradInverted();
        setCoverGrad();
        setButtonBgColor();
        setButtonTextColor();
    }, [setLowerGradInverted, setUpperGradInverted, setCoverGrad, setButtonBgColor, setButtonTextColor]);
 
    return {
        mainColor,
        clearColors,
        coverGrad,
        buttonTextColor,
        buttonBgColor,
        upperGradInverted,
        lowerGradInverted
    };
};
