import React, { useRef, useEffect, useCallback, useState} from 'react';
import { useAccount } from 'wagmi';
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import {connect} from 'react-redux';
import {useShader} from 'components/spore/sketch.js';
import './locked.scss';

const LockedNotification = ({clearPlayer}) => { 
    const {data: account, isError, isLoading} = useAccount();
    let [showConnectWallet, setShowConnectWallet] = useState(false);
    let ref = useRef();
    let [loaded, setLoaded] = useState();
    useEffect(() => {
        window.addEventListener("message", onMessage);
    }, [setLoaded]);

    const onMessage = useCallback((e) => {
        if (e.data === "loaded-lock-shader") {
            setLoaded(true);
        }
    }, [setLoaded]);
    
    return (
        <div
          ref={ref} className="locked-notification-background">
         <iframe className={loaded ? "loaded-shader" : ""} src="https://zequencer.mypinata.cloud/ipfs/QmUZqg4Ee2sDWqNxhAT5xrUUDY53as3VWrdWYWK5HomBgf/"/>
        {showConnectWallet && !account ?
         <div
           onClick={e => e.stopPropagation()}
           className="locked-notification">
          <div className="connect-button-wrapper">
            <ConnectButton
              accountStatus="address" 
              showBalance={{ smallScreen: false, largeScreen: false }}
              label="Connect Wallet"
            />
          </div>
         </div> : 
          <div
            onClick={e => e.stopPropagation()}
            className="locked-notification">
          <div className="logo-container">
            <img
              onClick={() => clearPlayer()}
              className="pushable spores-logo" src="/spores-logo-black.svg"/>
          </div>
            {showConnectWallet && account ? `It seems you dont't own a Spore yet. Get the full dose by purchasing a Genesis Spore.` : 
            `Enjoying this microdose of Spores? Get the full dose by purchasing a Genesis Spore.`}
            <div
              onClick={() => window.location.href="/buy?sold=false"}
              className="go-to-buy pushable">
                Buy Now
            </div>
            {!account && <div className="pushable already-own-one" onClick={() => setShowConnectWallet(true)}>
              Already own one?
                         </div>}
          </div>}
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LockedNotification);
