import React, { useRef, useState, useEffect } from 'react';
import {connect} from 'react-redux';
import {setPlayerRef} from 'actions/index.js';
import SimpleControls from './SimpleControls.js';
import {isMobile} from 'react-device-detect';
import SampleInfo from 'components/samples/SampleInfo.js';
import {useAccount} from 'wagmi';
import VideoShader from 'components/video/VideoShader.js';
import SporeRecorded from 'components/recording/SporeRecorded.js';
import { useSporeEmbed } from 'components/hooks/useSporeEmbed.js';
import { useSporeListener } from 'components/hooks/useSporeListener.js';
import SporeControls from 'components/nft-jacker/ShuffleButton.js';
import {useRemixListener} from 'components/hooks/useRemixListener';
import './spore-embed.scss';

const CID = 'QmWV7gqvBeXT9VVYEEL5J9oGEvQFDjYZZ4rgGYz9JDk3h7';
const DEFAULT_SPORE_URL = `https://zequencer.mypinata.cloud/ipfs/${CID}/?tokenId=40&gallery=true`;

const SporeEmbed = ({
    simpleControls,
    zorbId,
    isFullScreen,
    songTitle="Yo",
    collectionAddress,
    tokenId,
    artist,
    hideWaveform,
    thumbnail,
    setStems,
    stems,
    isWaveformRainbow,
    url= 'https://zequencer.mypinata.cloud/ipfs/QmNejpqTsVw3W3872o85XhWiKVG6gUchVJTvS4uUj2fUrC',
    innerControls,
    hideControls,
    children,
    playerRef,
    setPlayerRef,
    isSimple,
    color="white",
}) => {
    console.log("HIDE WAVEFORM=", hideWaveform);

    const {data: account, isError, isLoading} = useAccount();
    const ref = playerRef || useRef();

    let {totalRemixes, recordedRemix, uploadingRemix, onRemixSigningCompleted, remixToPlay, setRemixToPlay} = useRemixListener(
        () => [],
        ref
    );

    const {loaded} = useSporeListener(ref);

    useEffect(() => {
        console.log('sending zorb id=', zorbId);
        if (loaded && zorbId) {
            ref.current.contentWindow.postMessage({
                body: zorbId,
                type: "zorb-id"
            }, "*");
        }
    }, [zorbId, loaded]);
    useEffect(() => {
        if (!loaded) {
            return;
        }
        return;
        console.log('sending unlocked loops=', stems);
        setTimeout(() => {
            ref.current.contentWindow.postMessage({
                type: "unlocked-loops",
                body: stems
            }, "*");
        }, 1000);
    }, [stems, loaded]);

    useEffect(() => {
        setPlayerRef(ref);
    }, []);

    return (
        <div className="spore-embed-container">
          {children}
          <SporeRecorded
            uploadingRemix={uploadingRemix}
            recordedRemix={recordedRemix}
            collectionAddress={collectionAddress}
            spTokenId={1}
            onRemixSigningCompleted={onRemixSigningCompleted}
            tokenId={tokenId}
            playerRef={ref}/>
          {thumbnail && <img src={thumbnail} className={"cover-image"}/>}
          <div className={"spore-embed-helper"}>
            {!hideControls && innerControls &&
             <SporeControls
               hideWaveform={hideWaveform}
               innerControls={innerControls}
               isWaveformRainbow={isWaveformRainbow}
               buttonTextColor={color} playerRef={ref} />}
            <div className="song-title">
              {songTitle}
            </div>
            <div className="song-artist">
              {artist}
            </div>
            {!isSimple && <div className="stems-unlocked">
              {Math.min(11, stems)} / 11 STEMS UNLOCKED
              </div>}
          </div>
          <div className={"flex w-full"}>
            <iframe
              className={"spore-embed z-20 mx-auto"}
              width={isFullScreen ? window.innerWidth : 340}
              height={isFullScreen ? window.innerHeight : 400}
              ref={ref}
              src={url}
            />
            {simpleControls && <SimpleControls playerRef={ref}/>}
        {!hideControls && !innerControls && <SporeControls
              isWaveformRainbow={isWaveformRainbow}
                                              hideWaveform={hideWaveform}
                                              buttonTextColor={color} playerRef={ref} />}
            {!isMobile && !hideWaveform && <SampleInfo playerRef={ref}/>}
          </div>
        </div>
  )
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setPlayerRef: x => dispatch(setPlayerRef(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SporeEmbed);
