import React, { useRef, useEffect, useCallback, useState} from 'react';
import {vertexShader} from './blurred-shader/vertex.js';
import {fragmentShader} from './regular-shader/fragment.js';
import {isMobile} from 'react-device-detect';
import {useVideoSize} from './useVideoSize.js';
import {connect} from 'react-redux';
import {useVideoShader} from './useVideoShader.js';

const VideoContentShader = ({canvasRef, videoRef, uniforms, started}) => { 
    let ref = useRef();
    let {width, height} = useVideoSize(videoRef);
    useVideoShader({
        canvasRef,
        uniforms,
        resolution: 1,
        ref,
        videoRef,
        fragmentShader,
        vertexShader,
        width,
        height});
    let className = "video absolute md:h-2/3 md:w-auto md:top-1/2 md:left-1/2 md:transform md:-translate-x-1/2 md:-translate-y-1/2 w-full";
    if (isMobile && !started) {
        className += " hide";
    }
    return (
        <div style={{height, width}} ref={ref} className={className}>
        </div>);
};

export default VideoContentShader;
