import { ethers } from "ethers";
import React, { useEffect, useCallback, useState} from 'react';
import {sleep} from 'utils/sleep.js';

export const useTokensOnSale = () => {
    const [tokensOnSale, setTokensOnSale] = useState([]);

    useEffect(() => {
        // should query all of these along w buy-now price/whether theres an ask
        getTokensOnSale().then(setTokensOnSale);
        // for now just assume theyre for sale and for 0.4 eth
    }, [setTokensOnSale]);

    return {tokensOnSale, setTokensOnSale};
};

let cache = undefined;
let loading = false;
export const getTokensOnSale = () => {
    return new Promise(async (resolve) => {
        if (loading) {
            let i=0;
            while (!cache && i++ < 15) {
                await sleep(500);

            }
        }
        if (cache) {
            resolve(cache);
            return;
        }
        loading=true;
        let baseURL = "https://zequencer.io";
        if (window.LOCAL_BACKEND_FLAG && window.location.hostname.includes("localhost")) {
            baseURL = "";
        }

        let url = baseURL + "/nft-info";
        fetch(url)
            .then(r => r.json())
            .then(r => {
                cache = r;
                resolve(r);
            });
    });
};

