import color from "tinycolor2"
import hash from "string-hash"

export const getSporeHashIcon = (uid, {size}) => {
    const n = hash(uid)
    const c1 = color({ h: n % 360, s: 0.95, l: 0.5 })
    const c1_ = c1.toHexString()
    const c2 = c1.triad()[1].toHexString()

    return `<?xml version="1.0" encoding="UTF-8"?>
    <svg width="${size}px" height="${size}px" viewBox="0 0 61 61" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
      <defs>
      <circle cx="30.658" cy="30.9537" r="30" fill="url(#paint0_radial_151_38)" fill-opacity="0.58"/>
      <radialGradient id="paint0_radial_151_38" cx="0" cy="0" r="1" gradientUnits="userSpaceOnUse" gradientTransform="translate(30.658 30.9537) rotate(90) scale(30)">
            <stop stop-color="${c1_}"/>
            <stop offset="1" stop-color="${c2}" stop-opacity="0"/>
        </radialGradient>
      </defs>
      <g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
        <rect id="Rectangle" fill="url(#paint0_radial_151_38)" x="0" y="0" width="80" height="80"></rect>
      </g>
    </svg>`
}