import React, { memo, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

window.RATIO = .3;
const HEIGHT = 50;
const SongWaveform = memo(({progress, waveform, isWaveformRainbow=true, width=330}) => { 

    if (!waveform) {
        return '';
    }

    let parts = [];
    for (let i=0; i < waveform.length - 4; i+=4) {
        let height = Math.pow(waveform[i], window.RATIO)*(4/window.RATIO);
        let p = {
            height,
            y: HEIGHT/2 - height/2,
            r: waveform[i+1],
            g: waveform[i+2],
            b: waveform[i+3]
        };
        parts.push(p);
    }
    progress /= 100;
    return (
        <svg
          width={width}
          height={HEIGHT}
          className={!isWaveformRainbow ? "song-waveform  monochrome" : "song-waveform"}>
          {parts.map((part, i) =>
              <rect
                className={progress >= i / parts.length ? "elapsed" : ""}
                x={i*(width/parts.length)}
                y={part.y}
                width={2}
                height={part.height}
                fill={!isWaveformRainbow ? 'white' : `rgb(${part.r}, ${part.g}, ${part.b})`}/>)}
        </svg>
    );
});

export default SongWaveform;
