import React, { useRef, useEffect, useCallback, useState} from 'react';
import {getSporeRemixes} from 'spores/getSporeRemixes.js';
import {getSporeHashIcon} from 'spores/getSporeHashIcon.js';
import hashAvatar from 'hash-avatar';
import svgToTinyDataUri from 'mini-svg-data-uri';
import ENSName from 'components/ens/ENSName.js';
import {connect} from 'react-redux';
import { isMobile } from 'react-device-detect';
import './remixlist.scss'

const RemixList = ({
    creatorsToIgnore=[],
    spTokenId,
    setRemixToPlay,
    remixToPlay,
    collectionAddress,
    tokenId,
    clear
}) => {
    let [remixData, setRemixData] = useState([]);
    const [noRemixes, setNoRemixes] = useState();

    useEffect(() => {
        if (collectionAddress && tokenId) {
            getSporeRemixes(collectionAddress, tokenId)
                .then(result => {
                    result = result.filter(x => !creatorsToIgnore.includes(x.creator));
                    setRemixData(result);
                    setNoRemixes(result.length === 0 ? true : false);
                });
        }
    }, [setRemixData, collectionAddress, tokenId]);

    return (
        <div className='remix-list-container'>
            {isMobile && <div className='remix-close-button-container'><div className="pushable close-button" onClick={() => clear()}>x</div></div>}
            {noRemixes && <div className='no-remix-div'>No remixes have been made for this song yet. You could be the first...</div>}
            {remixData.map(x => 
                <RemixListItem
                  spTokenId={spTokenId}
                  isPlaying={x === remixToPlay}
                  setRemixToPlay={setRemixToPlay}
                  clear={clear}
                  data={x}/>
            )}
        </div>
        );
};

export const RemixListItem = ({clear, isPlaying, data, setRemixToPlay, spTokenId}) => {
    let hashedIcon = svgToTinyDataUri(getSporeHashIcon(data.sporeHash, {size: 60}));
    let className = "remix-list-item-container pushable";
    if (isPlaying) {
        className += " playing";
    }
    return(
        <div
          onClick={() => {
              setRemixToPlay(data);
              if (isMobile) {
                  clear();
              }
          }}
          className={className}>
            <div className='remix-icon' style={{backgroundImage: 'url("' + hashedIcon + '")'}}></div>
            <div className='remix-info-container'>
              {!data.sporeId ? <div className='remix-number'>Remix (Pending)</div>
               : <div className='remix-number'>Remix #{data.sporeId}</div>}
              <div className='remix-created-on'>Mixed on {spTokenId ? spTokenId : "SP-" + data.spTokenId}</div>
              <div className='remix-address'>By {data.sporeId ? <ENSName address={data.creator}/> : "You"}</div>
            </div>
        </div>
    );
};

const mapStateToProps = (state, props) => {
    return {
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
    };
  };
  
  export default connect(mapStateToProps, mapDispatchToProps)(RemixList);
