import {jack} from 'jacker/jack.js';
import {getTokenMetadata} from 'components/hooks/useTokenMetadata.js';

// JACK is essentially token metadata but standardized in a way that
// we can use in the platform
export const getJackFromToken = async (collectionAddress, tokenId) => {
    let link = "https://zora.co/collections/" + collectionAddress + "/" + tokenId;
    let jacked = await jack(link, getTokenMetadata);
    return jacked;
};
