import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import VisualLinks from './VisualLinks.js';
import {docs} from './docs.js';


const HelperLines = ({helperRefs, selected, sliderHovered}) => { 
    let ref= useRef();
    let doc = docs[selected];
    return [
        <div ref={ref} key={1} className="helper-box">
          {Array.isArray(doc) ? doc.map((x, i) => <p className={(selected.includes("spButton") || selected === "ledButton") && i===sliderHovered ? "selected" : ""}>{x}</p>) : doc}
        </div>,
        <VisualLinks helperBox={ref} helperRefs={helperRefs} selected={selected}/>
    ];
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HelperLines);
