import React, {useEffect, useState, useCallback} from 'react';
import {getJuicePlaylists} from 'spores/playlist.js';

export const usePlaylists = () => {
    let [playlists, setPlaylists] = useState([]);

    useEffect(() => {
        getJuicePlaylists().then(setPlaylists);
    }, []);

    return playlists;
};
