import React, { useEffect, useState} from 'react';
import {connect} from 'react-redux';
import {
    EmailIcon,
    EmailShareButton,
    FacebookMessengerIcon,
    FacebookMessengerShareButton,
    RedditIcon,
    RedditShareButton,
    TelegramIcon,
    TelegramShareButton,
    TwitterIcon,
    TwitterShareButton,
    WhatsappIcon,
    WhatsappShareButton,
  } from "react-share";
  import { BsShareFill } from 'react-icons/bs';
  import { FaRegClipboard } from 'react-icons/fa';
import { isMobile } from 'react-device-detect';

  const ShareButton = ({url, type, lowerGradInverted, buttonBgColor}) => {
    const [showShareModal, setShowModal] = useState(false);
    const [isCopied, setIsCopied] = useState(false);  
    const shareUrl = url;
    const title = "Check out this song on Spores!";

    function copyText() {
        {navigator.clipboard.writeText(url)};
        setIsCopied(true);
    }

    return (
        <>
            <div className='share-button pushable' onClick={() => setShowModal(true)}>
                <BsShareFill style={{fill: isMobile ? lowerGradInverted : buttonBgColor}}/>
            </div>
            {showShareModal && <div className='share-button-modal'>
                <div className='share-upper-container'>
                    <div><p>{type === "remix" ? "Share Remix" : "Share Song"}</p></div>
                    <div onClick={() => setShowModal(false)} className="share-close-button">x</div>
                </div>
                <div className='share-url-field'>
                    <p>{url}</p>
                    <FaRegClipboard className="pushable" style={{color: isCopied ? "#84ff98" : "white", cursor: "pointer"}} onClick={copyText}/>
                </div>
                <div className='share-icon-container'>
                    <div className='sharing-icon pushable'>
                        <TwitterShareButton
                            url={shareUrl}
                            title={title}>
                            <TwitterIcon size={32} round/>
                        </TwitterShareButton>
                    </div>
                    <div className='sharing-icon pushable'>
                        <FacebookMessengerShareButton
                            url={shareUrl}
                            quote={title}>
                            <FacebookMessengerIcon size={32} round/>
                        </FacebookMessengerShareButton>
                    </div>
                    <div className='sharing-icon pushable'>
                        <RedditShareButton
                            url={shareUrl}
                            title={title}>
                            <RedditIcon size={32} round/>
                        </RedditShareButton>
                    </div>
                    <div className='sharing-icon pushable'>
                        <TelegramShareButton
                            url={shareUrl}
                            title={title}>
                            <TelegramIcon size={32} round/>
                        </TelegramShareButton>
                    </div>
                    <div className='sharing-icon pushable'>
                        <WhatsappShareButton
                            url={shareUrl}
                            title={title}
                            separator=":: ">
                            <WhatsappIcon size={32} round/>
                        </WhatsappShareButton>
                    </div>
                    <div className='sharing-icon pushable'>
                        <EmailShareButton
                            url={shareUrl}
                            subject={title}>
                            <EmailIcon size={32} round/>
                        </EmailShareButton>
                    </div>
                </div>
            </div>}
            
        </>
    )
  }

  const mapStateToProps = (state, props) => {
    return {
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
    };
  };
  
  export default connect(mapStateToProps, mapDispatchToProps)(ShareButton);