import {signMessage} from './sign.js';
import {ipfsHashToBytes32} from 'utils/ipfs.js';

const sporeMessage = (x) => `A new spore awakens at ipfs://${x}`;

export const newSpore = (signer, spTokenId, collectionAddress, tokenId, recordingHash, dataHash) => {
    return new Promise(async (resolve) => {
        console.log('new spore w data hash=', dataHash);
        let message = sporeMessage(recordingHash);
        let {account, flatSignature, splitSignature} = await signMessage(signer, message);

        // now store this signed message on the ropsten contract
        let {r,s,v} = splitSignature;
        
        let body = {
            r,
            s,
            v,
            message,
            recordingHash: ipfsHashToBytes32(recordingHash),
            dataHash: ipfsHashToBytes32(dataHash),
            tokenId,
            spTokenId,
            collectionAddress,
        };
        
        let baseURL = "https://zequencer.io";
        if (window.location.hostname.includes("localhost")) {
            baseURL = "";
        }

        fetch(baseURL + '/newUnofficialSpore/' , {
            method: 'POST',
            body: JSON.stringify(body),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            }
        })
            .then(resp => resp.json())
            .then(resp => {
                resolve(resp.transactionHash);
            });
    });
};

