import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './animation.scss';

const ZorbAnimation = ({zorbStartPosition}) => { 
    let {image, x, y} = zorbStartPosition;
    let [animating, setAnimating] = useState(false);

    const set = (property, value) => {
        property = '--' + property;
        document.documentElement.style.setProperty(property, value);
    };

    useEffect(() => {
        let width = window.innerWidth;
        let height = window.innerHeight;
        set("zorb-start-x", x+-25+'px');
        set("zorb-start-y", y+-25+'px');
        set("zorb-mid-x", width/2-25+'px');
        set("zorb-mid-y", width/2-25+'px');
        set("zorb-end-x", width/2-25+'px');
        set("zorb-end-y", height/2-25+'px');
        setAnimating(true);
        setTimeout(() => {
            setAnimating(false);
        }, 1000);
    }, [zorbStartPosition, x, y, setAnimating]);

    if (!animating) {
        return '' ;
    }

    return (<img
              style={{left: x, top: y}}
              className="zorb-animation animate-it" src={image}/>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbAnimation);
