import React, { useRef, useEffect, useCallback, useState} from 'react';
import {isMobile} from 'react-device-detect';
import HelperLines from './HelperLines.js';
import {connect} from 'react-redux';
import './helper.scss';

const PlayerHelper = ({clear}) => { 
    let [sliderHovered, setSliderHovered]  = useState(undefined);
    let [selected, setSelected] = useState("centerButton");
    let centerButton = useRef();
    //let spButton1 = useRef();
    //let spButton2 = useRef();
    let ledButton = useRef();
    let slider1 = useRef();
    let slider2 = useRef();
    let slider3 = useRef();
    let slider4 = useRef();
    return (
        <div
          onTouchStart={(e) => {
              e.stopPropagation();
          }}
          onClick={(e) => {
              e.stopPropagation();
              clear();
          }}
          className="player-helper">
          <HelperLines
            sliderHovered={sliderHovered}
            selected={selected}
            helperRefs={{
              slider1,
              slider2,
              slider3,
              slider4,
              ledButton,
              //spButton1,
              //spButton2,
              centerButton
          }}/>
          <div onClick={clear} className="pushable x-button">
            {isMobile ? "Ready?" : "x"}
          </div>
          <div
            onTouchEnd={e => {
                console.log('stopping touch end');
                e.stopPropagation();
            }}
            onTouchStart={e => {
                console.log('stopping on touch start');
              e.stopPropagation();
              }}
            onMouseDown={e => {
                e.stopPropagation();
                console.log('stopping mouse down');
            }}
            onClick={e => {
                e.stopPropagation();
                console.log('stopping click');
            }}
            className="mock-player">
            <div ref={centerButton} className={selected === "centerButton" ? "center-button selected" : "center-button"}
                 onClick={(e) => {
                     e.stopPropagation();
                     setSelected("centerButton");
                 }}
                 onTouchStart={() => setSelected("centerButton")}
            />
            <div ref={ledButton} className={selected === "ledButton" ? "sp-button button1 led-button selected" : "sp-button button1 led-button"}
                 onTouchStart={() => setSelected("ledButton")}
                 onClick={(e) => {
                     e.stopPropagation();
                     setSelected("ledButton");
                 }}
            />
            <div
              onMouseLeave={() => setSliderHovered(undefined)}
              onMouseOver={() => setSliderHovered(1)}
              ref={slider1} className={selected === "slider1" ? "sp-slider slider1 selected" : "sp-slider slider1"}
              onClick={(e) => {
                  e.stopPropagation();
                  setSelected("slider1");
              }}
              onTouchStart={() => setSelected("slider1")}
            >
              <span>1</span>
            </div>
            <div
              onMouseLeave={() => setSliderHovered(undefined)}
              onMouseOver={() => setSliderHovered(4)}
              ref={slider2} className={selected === "slider2" ? "sp-slider slider2 selected" : "sp-slider slider2"}
              onClick={(e) => {
                  e.stopPropagation();
                  setSelected("slider2");
              }}
              onTouchStart={() => setSelected("slider2")}
            >
              <span>4</span>
            </div>
            <div
              onMouseLeave={() => setSliderHovered(undefined)}
              onMouseOver={() => setSliderHovered(3)}
              ref={slider3} className={selected === "slider3" ? "sp-slider slider3 selected" : "sp-slider slider3"}
              onClick={(e) => {
                  e.stopPropagation();
                  setSelected("slider3");
              }}
              onTouchStart={() => setSelected("slider3")}
            >
              <span>3</span>
            </div>
            <div
              onMouseLeave={() => setSliderHovered(undefined)}
              onMouseOver={() => setSliderHovered(2)}
              ref={slider4} className={selected === "slider4" ? "sp-slider slider4 selected" : "sp-slider slider4"}
              onClick={(e) => {
                  e.stopPropagation();
                  setSelected("slider4");
              }}
              onTouchStart={() => setSelected("slider4")}
            >
              <span>2</span>
            </div>
          </div>
        </div>
    );
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PlayerHelper);
