import React, { useRef, useEffect, useCallback, useState} from 'react';
import JuicePlaylists from './JuicePlaylists.js';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';

import {ethers} from 'ethers';
import {web3} from 'decentralized/web3.js';
import {getRemixesMap} from "spores/getSporeRemixes.js";
import {getChillRXSongs} from "spores/chillRX.js";
import {connect} from 'react-redux';
import {useScroll} from 'components/hooks/useScroll.js';
import {useTokensOnSale} from '../hooks/useTokensOnSale.js';
import './chart.scss';
import Collapsible from 'react-collapsible';
import PlaylistInfo from './PlaylistInfo.js';

const PINATAS = {
    "mintsongs": "mintsongs",
    "zora": "zora-prod",
    "soundxyz": "soundxyz",
    "zequencer": "zequencer",
    "catalog": "catalog",
};

// switch to true/false to enable charts as a feature
window.CHARTS_FLAG = true;

const SEARCH_TIME_OUT = 1000;

const Chart = ({searchTerm, jacked, loadJack, history=[], showChart, playlistLoaded}) => { 
    let [loading, setLoading] = useState(false);
    let [chartData, setChartData] = useState([]);
    let [remixes, setRemixes] = useState({});
    let [chillRXData, setChillRXData] = useState({});
    let searchCounter = useRef(0);
    let searchTermRef = useRef("");
    let cursor = useRef();
    let lastCursorUsed = useRef();
    let [keyonSongs, setKeyonSongs] = useState([]);
    let [selectedTab, setSelectedTab] = useState(playlistLoaded ? "playlists" : "new");
    let ref = useRef();
    let {setTokensOnSale, tokensOnSale} = useTokensOnSale();
    let searchId = useRef(0);

    console.log("SEARCH TERm=", searchTerm);
    useEffect(() => {
        getRemixesMap().then(setRemixes);
        setChillRXData(getChillRXSongs());
    }, []);

    useEffect(() => {
        searchTermRef.current = searchTerm;
    }, [searchTerm]);

    // size is a variable telling us how many items to show in the list
    // it updates as we scroll
    let {size} = useScroll(
        ref,
        Math.ceil((window.innerWidth / 200) * (window.innerHeight / 200)), // intial # to load
        searchTerm);

    useEffect(() => {
        lastCursorUsed.current = undefined;
        cursor.current = undefined;
    }, [showChart]);

    useEffect(() => {
        if (selectedTab !== "new") {
            return;
        }
        if (size >= chartData.length) {
            if (lastCursorUsed.current === cursor.current) {
                return;
            }
            // then we call search again
            let baseURL = window.location.hostname === "localhost" ?
                "http://localhost:9090/charts" :  "https://zequencer.io/charts";
            if (!window.LOCAL_BACKEND_FLAG) {
                baseURL = "https://zequencer.io/charts";
            }
            let url = baseURL;
            if (cursor.current && cursor.current !== "") {
                url += "?cursor=" + cursor.current;
            }
            lastCursorUsed.current = cursor.current;

            let id = ++searchId.current;
            fetch(url).then(async x => {
                let json = await x.json();
                if (/*searchId.current !== id ||*/ searchTermRef.current && searchTermRef.current !== "") {
                    return;
                }
                if (json) {
                    setLoading(false);
                    setChartData([... chartData, ...json.results]);
                    cursor.current = json.cursor;
                }
            });
        }
    }, [setChartData, selectedTab, chartData, size]);

    useEffect(() => {
        if (searchTerm !== "") {
            setSelectedTab("new");
        }
    }, [searchTerm, setSelectedTab]);

    useEffect(() => {
        if (playlistLoaded) {
            setSelectedTab("playlists");
        }
    }, [playlistLoaded]);

    useEffect(() => {
        console.log("SEARCH TERM CHANGED");
        if (!window.CHARTS_FLAG) {
            return;
        }

        if (selectedTab == "history") {
            // set to history data
            setChartData(history);
        } else if (selectedTab == "new") {
            // set to chart/new data
            let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/charts" :  "https://zequencer.io/charts";
            if (!window.LOCAL_BACKEND_FLAG) {
                baseURL = "https://zequencer.io/charts";
            }
            if (searchTerm && searchTerm !== "") {
                let id = ++searchId.current;
                setTimeout(() => {
                    if (id !== searchId.current) {
                        return;
                    }
                    setLoading(true);
                    let _searchTerm = searchTerm;
                    if (_searchTerm.toLowerCase().includes("chaos")) {
                        _searchTerm = _searchTerm.replace("#", "");
                    }
                    let url = baseURL + "/" + _searchTerm;
                    cursor.current = undefined;
                    fetch(url).then(async x => {
                        if (id !== searchId.current) {
                            return;
                        }
                        let json = await x.json();
                        setLoading(false);
                        if (json) {
                            setChartData(json.results);
                            cursor.current = undefined; //json.cursor;
                        } 
                    });
                }, SEARCH_TIME_OUT);
            } else {
                setLoading(true);
                let url = baseURL;
                console.log("BASE URL SEARCh=", url);
                fetch(url).then(async x => {
                    let json = await x.json();
                    if (json) {
                        setLoading(false);
                        setChartData(json.results);
                        cursor.current = json.cursor;
                    }
                });
            }
        }
    }, [history, setChartData, searchTerm, jacked, selectedTab, cursor]);

    return (<>
              <div
                onClick={e => e.stopPropagation()}
                className='tab-container' style={{display: showChart ? "flex" : "none"}}>
            {window.PLAYLIST_FLAG && <div
            className={selectedTab == "playlists" ? "tab pushable selected" : "tab pushable not-selected"}
            onClick={() => setSelectedTab("playlists")}><p>Playlists</p></div>}
            
            <div className={selectedTab == "history" ? "tab pushable selected" : "tab pushable not-selected"}
            onClick={() => setSelectedTab("history")}><p>Recently Played</p></div>
            <div className={selectedTab == "new" ? "tab pushable selected" : "tab pushable not-selected"}
            onClick={() => setSelectedTab("new")}><p>New Songs</p></div>
        </div>

        {/* PLAYLIST */}
        { selectedTab == "playlists" &&
          <JuicePlaylists loadJack={loadJack} playlistLoaded={playlistLoaded}/>}

        {/* CHART */}
        {selectedTab != "playlists" &&
        <div
          ref={ref}
          onClick={e => e.stopPropagation()}
          className="music-chart" style={{display: showChart ? "flex" : "none"}}>
            {loading ? <UnknownProgressBar width={140} text="Searching the metaverse"/> : chartData.slice(0, size).map(x =>
                <div
                  className="chart-item">
                  <SongInfo
                    remixes={remixes}
                    selectedTab={selectedTab} loadJack={loadJack} data={x.jack ? {... x, token: {
                    ... x,
                    metadata: x.jack
                }} :  x}/>
              </div>)
            }
        </div>}
    </>);
};

export const SongInfo = ({data, loadJack, tokenId, thumbnail, selectedTab, askPrice, remixes, section}) => {
    let [thumb, setThumb] = useState();
    let artist = '';
    let song = '';
    let coverArt = '';
    let externalLink = '';
    let collectionName = '';
    let possibleThumbnail = undefined;

    if (selectedTab == "playlists") {
        // SPORE TAB
        if (section == "chill-rx-2022") {
            artist = "ChillRX";
            song =  data.songName;
            coverArt = data.coverArt;
            externalLink = "/";
            collectionName = "ChillRX Remix Contest";
        } else {
            artist = "Psilocybin EP";
            song =  "SP-" + tokenId;
            coverArt = thumbnail;
            externalLink = "/buy";
            collectionName = "Spore Season 1: Genesis";
        }
    } else if (selectedTab == "new") {
        // NEWLY MINTED TAB
        // mintsongs & catalog & chaos
        if (data.token.collectionName != "Zora") {
            artist = data.token.metadata.artist;
            song = data.token.metadata.title || data.token.metadata.name;
            coverArt = "" + data.token.metadata.image;

            // chaos external url
            if (data.token.collectionName == "Chaos") {
                externalLink = "https://opensea.io/assets/ethereum/" + data.token.collectionAddress + "\/" + data.token.tokenId;
            } else {
                externalLink = data.token.metadata.external_url;
            }
            
        } else {
        // zora
            song = data.token.name;
            externalLink = "https://zora.co/collections/zora/" + data.token.tokenId;
            
            if (data.token.metadata.body) {
                let body = data.token.metadata.body;
                if (body.artwork && body.artwork.info && body.artwork.info.uri) {
                    coverArt = body.artwork.info.uri;
                    if (coverArt.includes("ipfs.io")) {
                        let part = "ipfs.io/";
                        let index = coverArt.indexOf(part);
                        coverArt = "https://zora-prod.mypinata.cloud/" + coverArt.slice(index+part.length);
                        possibleThumbnail = `https://d2iccaf7eutw5f.cloudfront.net/0xabEFBc9fD2F806065b4f3C237d4b59D9A97Bcac7/${data.token.tokenId}/thumbnail`;
                    }
                }
            } else {
                coverArt = data.token.metadata.image || "/zora-placeholder.png";
            }
        }

        collectionName = data.token.collectionName;
        
    } else {
        // HISTORY TAB
        artist = data.token.metadata.artist;
        song = data.token.metadata.title || data.token.metadata.name;
        coverArt = "" + data.token.metadata.image;
        externalLink = data.token.metadata.external_url || data.token.metadata.link;
        collectionName = "View source"

        if (data.token.metadata.image == undefined) {
            coverArt = "/zora-placeholder.png";
        }

        // Chaos external link
        if (data.token.metadata.artist == "Chaos") {
            externalLink = "https://opensea.io/collection/chaos-songs";
        }
    }

    // load cover image if not from zora for NEWLY MINTED TAB
    if (!coverArt.includes("zora")) {
        if (coverArt.includes("ipfs.io")) {
        } else if (coverArt.startsWith("ipfs://")) {
            let _coverArt;
            for (let type in PINATAS) {
                if (externalLink && externalLink.includes(type)) {
                    _coverArt = `https://${PINATAS[type]}.mypinata.cloud/ipfs/${coverArt.slice(7)}`;
                }
            }
            coverArt = _coverArt || "https://zora-prod.mypinata.cloud/ipfs/" + coverArt.slice(7);
        }
    }

    const onClick = useCallback(() => {
        let ret = {};
        if (selectedTab == "playlists") {

        } else {
            if  (data.jack) {
                loadJack(data.jack);
                return;
            } else {
                ret = {... data.token.metadata };
                ret.url = data.token.content.url;
                ret.link = externalLink;
                ret.tokenId = data.token.tokenId;
                ret.collectionAddress = data.token.collectionAddress;
                loadJack(ret);
            }
        }
        
    }, [data, loadJack, externalLink, tokenId]);

    let numRemixes = 0;
    if (selectedTab == "new" && data && data.token && data.token.collectionAddress) {
        let collectionAddress;
        let {tokenId} = data.token;
        if (externalLink && externalLink.includes("catalog")) {
            collectionAddress = "0x0bC2A24ce568DAd89691116d5B34DEB6C203F342";
        } else if (externalLink && externalLink.includes("mintsongs")) {
            collectionAddress = "0x2B5426A5B98a3E366230ebA9f95a24f09Ae4a584";
        }
        if (collectionAddress) {
            coverArt = `https://d2iccaf7eutw5f.cloudfront.net/${collectionAddress}/${tokenId}/thumbnail`;
        }
        collectionAddress = data.token.collectionAddress.toLowerCase();
        let _token = tokenId;
        if (tokenId.length > 10) {
            let big = ethers.BigNumber.from(tokenId);
            big = web3.utils.padLeft(big.toHexString(), 64).toLowerCase();
            _token = big;
        }
        if (remixes[collectionAddress] && remixes[collectionAddress][_token]) {
            numRemixes = remixes[collectionAddress][_token].length;
        }
    } else if (selectedTab == "history" && data && data.jack && data.jack.collectionAddress) {
        let collectionAddress;
        let {tokenId} = data.jack;
        if (externalLink && externalLink.includes("catalog")) {
            collectionAddress = "0x0bC2A24ce568DAd89691116d5B34DEB6C203F342";
        } else if (externalLink && externalLink.includes("mintsongs")) {
            collectionAddress = "0x2B5426A5B98a3E366230ebA9f95a24f09Ae4a584";
        }
        collectionAddress = data.jack.collectionAddress.toLowerCase();
        let _token = tokenId;
        if (tokenId.length > 10) {
            let big = ethers.BigNumber.from(tokenId);
            big = web3.utils.padLeft(big.toHexString(), 64).toLowerCase();
            _token = big;
        }
        if (remixes[collectionAddress] && remixes[collectionAddress][_token]) {
            numRemixes = remixes[collectionAddress][_token].length;
        }
    }

           
    return (<>
              <div
                className="song-info-container">
                <img
                  onError={() => setThumb(coverArt)}
                  className="cover-image pushable" loading="lazy" src={thumb || possibleThumbnail || coverArt} onClick={onClick}></img>
                <div className="song-info">
                  <div className="song-col">
                    <div className="song">{song}</div>
                    <div className="artist">{artist}</div>
                    <div className="from-text">
                      <a href={externalLink}>{collectionName}</a>
                    </div>
                  </div>
                  {(numRemixes > 0) && <div className="num-remixes">
                                         <div className="remixes-orb"/>{numRemixes}</div>}
                </div>
              </div>
        
    </>);
};

const mapStateToProps = (state, props) => {
  return {
      playlistLoaded: state.playlist.playlistLoaded
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Chart);
