// 0-1 values of each "effect" slider
export const SPACE_SLIDER_VALUE = "space-slider-value";
export const FILTER_SLIDER_VALUE = "filter-slider-value";
export const JUICE_SLIDER_VALUE = "juice-slider-value";

// realtime signal strength of the "space effect"
export const SPACE_EFFECT_LEVELS = "space-effect-levels";

// realtime signal strength of the "gate effect"
export const GATE_STRENGTH = "gate-strength";

// sample-waveform is a list of "height" values for the waveform
// one for each "x-position". allows you to draw waveform
// in 2D
export const SAMPLE_WAVEFORM = "sample-waveform";

// song-bpm is the initial bpm of the song
export const SONG_BPM = "song-bpm";

// playback-bpm is the current bpm (after pitching down/up)
// ratio of playback-bpm/song-bpm is the "playback speed"
export const PLAYBACK_BPM = "playback-bpm";

// mapping of message type ---> uniform name
export const TYPE_TO_NAME = {
    "space-slider-value": "spaceSliderValue",
    "filter-slider-value": "filterSliderValue",
    "juice-slider-value": "juiceSliderValue",
    "space-effect-levels": "spaceEffectLevels",
    "gate-strength": "gateStrength",
    "song-bpm": "songBPM",
    "playback-bpm": "playbackBPM",
    "is-rolling": "isRolling",
    "rolling-rate": "rollRate",
};
