import React, { useEffect, useCallback, useRef, useState} from 'react';
import ipfs from 'decentralized/Ipfs.js';
import {cropAndStore} from 'utils/cropAndStore.js';
import {newSpore} from 'spores/newSpore.js';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import { useSigner } from 'wagmi';
import {isMobile} from 'react-device-detect';
import {useWaveformPlayer} from './useWaveformPlayer.js';
import PlayButton from 'components/buttons/PlayButton.js';
import {connect} from 'react-redux';
import SliceWaveformSVG from './SliceWaveformSVG.js';
import Captioner from './Captioner.js';
import './slice-waveform.scss';

const SliceWaveform = ({waveform, buffer, onCancel}) => { 
    let ref = useRef();
    const {data: signer} = useSigner();
    let [caption, setCaption] = useState('');
    let [downloading, setDownloading] = useState(false);
    let [isCaptioning, setIsCaptioning] = useState(false);
    let [width, setWidth] = useState(300);
    let [start, setStart] = useState(0);
    let [length, setLength] = useState(100);
    let [draggingStart, setDraggingStart] = useState();
    let [draggingEnd, setDraggingEnd] = useState();
    let [download, setDownload] = useState();


   useEffect(() => {
       let _width = isMobile ? 380 : ref.current.offsetWidth;
       let width = (_width - 140)/2;
       setWidth(width);
    }, [setWidth]);

    const onMouseMove = useCallback((e) => {
        if (draggingStart === undefined && draggingEnd === undefined) {
            return;
        }
        if (e.touches) {
            e = e.touches[0];
        }
        let rect = ref.current.getBoundingClientRect();

        let x = e.pageX - rect.left;
        if (draggingStart !== undefined) {
            let diff = 100*(x - draggingStart.x) / (width*2);
            let newStart = draggingStart.start + diff;
            newStart = Math.max(0, Math.min(100, newStart));
            setStart(newStart);
            setLength(Math.min(100 - newStart, draggingStart.length - diff));
        } else {
            let diff = 100*(x - draggingEnd.x) / (width*2);
            let newLength = draggingEnd.length + diff;
            setLength(Math.min(100 - start, Math.max(0, newLength)));
        }

    }, [draggingStart, width, draggingEnd, start, length, setLength, setStart]);

    const dragStartControl = useCallback((e) => {
        if (e.touches) {
            e = e.touches[0];
        }
        let rect = ref.current.getBoundingClientRect();
        let x = e.clientX - rect.left;
        setDraggingStart({x, start, length});
    }, [setDraggingStart, start, length]);

    const dragEndControl = useCallback((e) => {
        if (e.touches) {
            e = e.touches[0];
        }
        let rect = ref.current.getBoundingClientRect();
        let x = e.clientX - rect.left;
        setDraggingEnd({x, length});
    }, [setDraggingEnd, length]);

    const onMouseUp = useCallback(() => {
        setDraggingStart(undefined);
        setDraggingEnd(undefined);
    }, [setDraggingEnd, setDraggingStart]);

    const {playhead, stop, play, isPlaying} = useWaveformPlayer({buffer, start, length});

    const post = useCallback(async () => {
        stop();
        setDownloading(true);
        let dataToStore = {caption};
        /**
           1. Crop and store on ipfs
           2. Store caption on ipfs
         */
        let cropped = await cropAndStore(buffer, start, length);
        setDownload(cropped);
        setDownloading(false);
    }, [caption, start, setDownloading, length, setDownload]);

    useEffect(() => {
        window.addEventListener("mousemove", onMouseMove);
        window.addEventListener("touchmove", onMouseMove);
        window.addEventListener("mouseup", onMouseUp);
        window.addEventListener("touchend", onMouseUp);
        return () => {
            window.removeEventListener("mousemove", onMouseMove);
            window.removeEventListener("touchmove", onMouseMove);
            window.removeEventListener("touchend", onMouseUp);
            window.removeEventListener("mouseup", onMouseUp);
        };
    }, [draggingStart, width, draggingEnd, start, length, setLength, setStart,
        setDraggingStart, setDraggingEnd
       ]);

    return (
        <div className="slicer-backdrop">
          {downloading ? <div className="download-test">
                           <UnknownProgressBar/>
        </div> : download ?  <div className="download-test">
         Download your remix <a target="_blank" href={"https://zequencer.mypinata.cloud/ipfs/" + download}>here</a>. We're still working on some things :)
                    </div> : <div
            className="slice-waveform">
            <div className="control-btn-container">
              <PlayButton isPlaying={isPlaying} play={play}/>
            </div>
            <div
              ref={ref} 
              className="waveform-container">
              {isCaptioning ? <Captioner setCaption={setCaption} caption={caption}/> : <SliceWaveformSVG
                                                                                         height={isMobile ? 60 : 100}
                                                                                         start={start} length={length} waveform={waveform} width={width}/>}
              {!isCaptioning && <div style={{left: 30 + (playhead/100)*(width)*2+'px'}} className="playhead"/>}
              {!isCaptioning && <div
                                  style={{left: start/100*(width*2)+'px', width: 60+length/100*(width*2)+"px"}}
                                  className="slicer">
                                  <div
                                    onMouseDown={dragStartControl}
                                    onTouchStart={dragStartControl}
                                    className="start-control">
                                    <div className="start-info">
                                      {Math.round(100*start/100*buffer[0].length/44100)/100} sec <span className="label">start</span>
                                    </div>
                                    <div className="arrow-left"/>
                                  </div>
                                  <div
                                    onTouchStart={dragEndControl}
                                    onMouseDown={dragEndControl}
                                    className="end-control">
                                    <div className="arrow-right"/>
                                  </div>
                                </div>}
            </div>
            <div className="slice-guide-info">
              Slice your remix to mint your favorite moment, for free (just gas).
            </div>
           </div>}
          {downloading ? '' : download ? <div className="slice-toolbar">
                        <div onClick={onCancel} className="done-btn pushable continue">
                          Continue
                        </div>
           </div> : <div className={isCaptioning ? "slice-toolbar captioning" : "slice-toolbar"}>
            <div onClick={isCaptioning ? () => setIsCaptioning(false) : onCancel} className="cancel-btn pushable">
              {isCaptioning ? "Edit" : "Cancel"}
            </div>
            <div onClick={() => {
                if (isCaptioning) {
                    post();
                } else {
                    setIsCaptioning(true);
                }
            }} className="done-btn pushable">
              {isCaptioning ? "Mint" : "Next"}
            </div>
                    </div>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SliceWaveform);
