import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import Colors from 'components/Colors.js';
import Tone from 'tone';
const colors = new Colors();
window.heatRoot = 1.1;
//window.bucketSize = 4;
window.bucketSize = 1;
window.saturation = 80;

const SporeHeatMap = ({heat, width=54, height=10, center=false, saturation=200, duration}) => { 
    // lets start with slider data
    if (!heat) {
        return '';
    }

    // first process the heat
    let bucketSize = width === 54 ? window.bucketSize*2 : window.bucketSize;
    let buckets = getBuckets(heat, bucketSize);
    let max = Math.max(...Object.values(buckets))*.75;
    let maxStep = Math.max(...Object.keys(buckets));
    /*
    if (duration) {
        let oldMaxStep = maxStep;
        maxStep = (duration/new Tone.Time('32n'))/(bucketSize);
        console.log("max step=%s old max step=%s dur=%s", maxStep, oldMaxStep, duration);
    }
    */
    return (
        <svg className="heatmap" width={width} height={10}>
          {Object.keys(buckets).map(
              step => <rect
                        width={width > 80 ? 4 : 1}
                        height={center ? height* Math.min(1, Math.pow(buckets[step], window.heatRoot)/max) : height}
                        opacity={Math.min(1, Math.pow(buckets[step], window.heatRoot)/max)}
                        fill={colors.warm(190*Math.min(1, Math.pow(buckets[step], window.heatRoot)/max), 190,
                                          Math.min(1, Math.pow(buckets[step], window.heatRoot)/max)*saturation)}
                        x={width*step/maxStep}
                        y={center ? height/2 - height* Math.min(1, Math.pow(buckets[step], window.heatRoot)/max)/2 : 0}/>)}
        </svg>);
};

const getBuckets = (heat, bucketSize) => {
    let sliderData = heat.sliderChanges;
    let modeData = heat.modeChanges;
    let regions = sliderData.map(x => Math.round((isNaN(x[0]) ? 0 : x[0]) / bucketSize));
    let buckets = {};
    for (let x of regions) {
        if (!buckets[x]) {
            buckets[x] = 0;
        }
        buckets[x]+=1;
    }

    regions = modeData.map(x => Math.round((isNaN(x[0]) ? 0 : x[0]) / bucketSize));
    let max = Math.max(...Object.values(regions));
    for (let x of regions) {
        if (!buckets[x]) {
            buckets[x] = 0;
        }
        buckets[x]+=16;
        let q=0;
        for (let i=x+1; i < max; i++) {
            if (regions.some(x => x === i)) {
                break;
            }
            if (!buckets[i]) {
                buckets[i] = 0;
            }
            buckets[i] += (50+q);
            q+=8;
        }
    }
    return buckets;
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SporeHeatMap);
