import {decodeMP3} from 'utils/mp3.js';
import Tone from 'tone';

const bufferToArrayBuffer = require('buffer-to-arraybuffer');

let cache = {};
export const decodeAudioData = (blob, hash) => {
    return new Promise(async (resolve, reject) => {
        if (cache[hash]) {
            return resolve(cache[hash]);
        }
        if (blob === undefined) {
            reject();
            return;
        }
        try {
            try {
                let sliced = blob.slice(0,4);
                let type = new TextDecoder().decode(sliced);
                if (type === "RIFF") {
                    let r = await Tone.Master.context.decodeAudioData(blob);
                    console.log('r =', r);
                    if (hash) {
                        cache[hash] = r;
                    }
                    resolve(r);
                } else {
                    let r = await decodeMP3(new Int8Array(blob));
                    if (hash) {
                        cache[hash] = r;
                    }
                    resolve(r);
                }
            } catch (e) {
                reject(e);
            }
        } catch (e) {
            reject(e);
        }
    });
};

export const clearDecodeCache = () => {
    cache = {};
};
