const PINATAS = {
    "mintsongs": "mintsongs",
    "zora": "zora-prod",
    "sound.xyz": "soundxyz",
    "zequencer": "zequencer",
    "catalog": "catalog",
};

export const extractIPFS = (link) => {
    if (link.startsWith("ipfs://")) {
        return link.slice("ipfs://".length);
    }
    let part = "/ipfs/";
    let index = link.indexOf(part);
    let sliced = link.slice(part.length + index);
    return sliced;
};

export const getGatewayLink = (externalLink, ipfs) => {
    if (!externalLink) {
        console.log("NO EXTERNAL LINK FOUND FOR THIS LINK!");
        return "https://zora-prod.mypinata.cloud/ipfs/" + ipfs;
    }
    for (let type in PINATAS) {
        if (externalLink.includes(type)) {
            return "https://" + PINATAS[type] + '.mypinata.cloud/ipfs/' + ipfs;
        }
    }
    return "https://zora-prod.mypinata.cloud/ipfs/" + ipfs;
};
