import {CHILL_RX_COLLECTION} from 'constants/collections.js';
import {useInterval} from './useInterval.js';
import {useTokensOnSale} from './useTokensOnSale.js';
import { useAccount } from 'wagmi';
import React, {useRef, useEffect, useCallback, useState} from 'react';
import {CHEAT_CODES} from 'constants/cheat-codes.js'

const TICK_TIME = 1000;
const LOCK_TIME = 5 * 60 * 1000;

const isValidCheatCode = (code) => {
    return CHEAT_CODES.some(x => code.toLowerCase().trim() === x);
};

export const useTokenLock = (
    cheatcode,
    setLink,
    setUsedCheatCode,
    setSecondsRemaining
) => {
    const {data: account, isError, isLoading} = useAccount();
    const {tokensOnSale} = useTokensOnSale();
    let [cheatCodeActivating, setCheatCodeActivating] = useState();
    let [locked, setLocked] = useState(false);
    let [isSporeOwner, setIsSporeOwner] = useState();
    let timeElapsed = useRef(0);
    let isPlaying = useRef(false);
    let [cheatOverriden, setCheatOverriden] = useState(false);

    useEffect(() => {
        if (isValidCheatCode(cheatcode)) {
            setCheatOverriden(true);
            setLocked(false);
            window.localStorage.setItem("cheatcode", cheatcode);
            setUsedCheatCode(true);
            setCheatCodeActivating(true);
            setTimeout(() => {
                setLink('');
            }, 1000);
            setTimeout(() => {
                setCheatCodeActivating(false);
            }, 10000);
        }
    }, [cheatcode, setCheatOverriden, setLocked, setCheatCodeActivating]);

    useEffect(() => {
        let code = window.localStorage.getItem("cheatcode");
        if (code && isValidCheatCode(code)) {
            setCheatOverriden(true);
            setUsedCheatCode(true);
            setLocked(false);
        }
    }, []);

    useEffect(() => {
        if (window.OWN_FAKE_SPORE_FLAG) {
            setIsSporeOwner(true);
            setLocked(false);
            return;
        }
        if (tokensOnSale && account && account.address) {
            let _tokensOnSale = window.OWN_FAKE_SPORE_FLAG ? tokensOnSale.map(x =>
                ({
                    ... x,
                    owner: account.address})) : tokensOnSale;
            let address = account.address.toLowerCase();
            if (_tokensOnSale.some(x =>
                x &&
                    x.owner &&
                    x.owner.toLowerCase() === address)) {
                setIsSporeOwner(true);
                setLocked(false);
            }
        } else {
        }
    }, [tokensOnSale, account, setIsSporeOwner, setLocked]);

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
        let _timeElapsed = window.localStorage.getItem("time-elapsed") || 0;
        if (window.ABOUT_TO_END_FLAG) {
            timeElapsed.current = LOCK_TIME - 20*1000; // parseInt(_timeElapsed);
        } else {
            timeElapsed.current = parseInt(_timeElapsed);
        }
    }, []);

    const onMessage = useCallback((e) => {
        if (e.data && Array.isArray(e.data.type) && e.data.type[0] === "sequencer.start") {
            isPlaying.current = true;
        } else if (e.data && Array.isArray(e.data.type) && e.data.type[0] === "sequencer.stop") {
            isPlaying.current = false;
        }
    }, []);


    // listen for play events
    const tick = useCallback(() => {
        if (!window.TOKEN_LOCK_FLAG || cheatOverriden) {
            return;
        }
        if (timeElapsed.current >= LOCK_TIME && !locked) {
            if (!isSporeOwner) {
                setLocked(true);
            }
        }
        if (isPlaying.current && !isSporeOwner) {
            timeElapsed.current += TICK_TIME;
            let secondsRemaining = LOCK_TIME/1000 - timeElapsed.current/1000;
            setSecondsRemaining(secondsRemaining);
            window.localStorage.setItem("time-elapsed", timeElapsed.current);
        }
    }, [locked, setLocked, isSporeOwner, cheatOverriden]);

    useInterval(tick, TICK_TIME);

    return {locked, cheatCodeActivating};
};
