import {SET_SPORE_TOKEN_ID} from 'constants/action-types.js';
import {SET_SECONDS_REMAINING} from 'constants/action-types.js';
import {SET_SOLD_PLAYERS} from 'constants/action-types.js';
import {SET_PENDING_SPORES} from 'constants/action-types.js';
import {SET_USED_CHEAT_CODE} from 'constants/action-types.js';
import {SET_PLAYER_REF} from 'constants/action-types.js';

export const sporeReducer = (state = {}, action) => {
    let newState;
    switch (action.type) {
    case SET_PLAYER_REF:
        return {
            ...state,
            playerRef: action.payload
        };
    case SET_SECONDS_REMAINING:
        return {
            ... state,
            secondsRemaining: action.payload
        };
    case SET_USED_CHEAT_CODE:
        return {
            ... state,
            usedCheatCode: action.payload
        };
    case SET_SPORE_TOKEN_ID:
        return {
            ... state,
            tokenId: action.payload
        };
    case SET_PENDING_SPORES:
        return {
            ... state,
            pendingSpores: action.payload
        };
    case SET_SOLD_PLAYERS:
        return {
            ... state,
            soldPlayers: action.payload
        };
    default:
        return state;
    }
}
 
