import React, { useEffect, useCallback, useState} from 'react';
import {isMobile} from 'react-device-detect';
import {setSoldPlayers} from 'actions/index.js';
import BuyPlayerFlow from 'components/buy/BuyPlayerFlow.js';
import {connect} from 'react-redux';
import {useTokensOnSale} from '../hooks/useTokensOnSale.js';
import './buy-unsold.scss';

const BuyUnsold = ({upperGradInverted, soldPlayers=[], tokenId, text, showRemaining, onCompleteSale}) => { 
    let {setTokensOnSale, tokensOnSale} = useTokensOnSale();
    let [buying, setBuying] = useState(false);

    if (!tokensOnSale || tokensOnSale.length === 0) {
        return '';
    }

    let unsold = tokensOnSale.filter(x => x.askPrice);
    if (unsold.length === 0) {
        return '';
    }
    if (soldPlayers.includes(tokenId)) {
        return (
            <div className="buy-unsold">
              Owned By You
            </div>);
    }
    let style = {};
    if (upperGradInverted && text) {
        style.color = upperGradInverted;
        style.borderBottomColor = upperGradInverted;
    }
    return (
        <div className={text ? "buy-unsold has-text" : "buy-unsold"}>
          {buying && <BuyPlayerFlow setCompletedBuy={() => {
              setSoldPlayers([... soldPlayers, tokenId]);
              if (onCompleteSale) {
                  onCompleteSale(); // call optional callback
              }
          }} setBuying={setBuying} tokenId={tokenId}/>}
          <div
            style={style}
            onClick={() => setBuying(true)}
            className="buy-unsold-btn pushable">
            {text ? text : <span>
                             Buy SP-{tokenId}
                           </span>}
          </div>
          {((!isMobile && !text) || showRemaining) &&
           <a
             href={"/sp/" + tokenId}
             className="unsold-remaining">{unsold.length} remaining</a>}
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
      soldPlayers: state.spore.soldPlayers
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSoldPlayers: x => dispatch(setSoldPlayers(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(BuyUnsold);
