import React, { useRef, useEffect, useCallback, useState} from 'react';
import SampleInfo from 'components/samples/SampleInfo.js';
import {isUnlockedCollection} from 'locks/isUnlocked.js';
import {CHAOS_COLLECTION, CHILL_RX_COLLECTION} from 'constants/collections.js';
import RemixPlayer from 'components/remix/RemixPlayer.js';
import ShuffleButton from './ShuffleButton.js';
import TimeRemaining from '../lock/TimeRemaining.js';
import {useRemixListener} from 'components/hooks/useRemixListener.js';
import SporeRecorded from "components/recording/SporeRecorded.js";
import {useCoverArtColors} from 'components/hooks/useCoverArtColors.js';
import {useJacker} from 'components/hooks/useJacker.js';
import {
    setSecondsRemaining,
    setUsedCheatCode} from 'actions/index.js';
import {isMobile} from 'react-device-detect';
import {useLatestJacks} from 'components/hooks/useLatestJacks.js';
import {useFlags} from 'flags/flags.js';
import EchoSearchBox from './EchoSearchBox.js';
import TokenMetadata from 'components/metadata/TokenMetadata.js';
import {useTokenLock} from 'components/hooks/useTokenLock.js';
import LockedNotification from 'components/lock/LockedNotification.js';
import Chart from '../chart/Chart.js';
import PlayerHelper from 'components/spore/PlayerHelper.js';
import {KEYON_SONGS} from './History.js';
import Info from './Info.js'
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import JackerPlayer from './JackerPlayer.js';
import {jack} from 'jacker/jack.js';
import {save} from 'jacker/save.js';
import {getArtistFromMetadata} from 'jacker/artist.js';
import {connect} from 'react-redux';
import './nft-jacker.scss';
import './mobile-nft-jacker.scss';
import {submitSplitJob} from 'jacker/stem-splitter.js';
import { prominent } from 'color.js';
import Navigation from './Navigation.js'
import invert, { RGB, RgbArray, HexColor, BlackWhite } from 'invert-color';
import './connect-buttons.scss';
import RemixList from '../remix/RemixList.js';
import { HiOutlineChevronDoubleDown } from 'react-icons/hi'
import ShareButton from './ShareButton.js';
import SwipeableViews from 'react-swipeable-views';
import { IoMdArrowBack } from 'react-icons/io';

const NFTJacker = (props) => { 

    useFlags();
    let ref = useRef();
    let playerRef = useRef();

    const [link, setLink] = useState(props.defaultSearch || '');
    const [showHelp, setShowHelp] = useState(false);
    const [showHistory, setShowHistory] = useState(false);
    const {latestJacks, setLatestJacks} = useLatestJacks();
    const [showInfo, setShowInfo] = useState(false);
    const [showLinkInput, setShowLinkInput] = useState(true);
    const [showRemixes, setShowRemixes] = useState(!isMobile);

    const [passed, setPassed] = useState(false);
    const [failed, setFailed] = useState(false);
    const [password, setPassword] = useState("");

    useEffect(() => {
        window.addEventListener("click", clearChart);
        return () => window.removeEventListener("click", clearChart);
    }, [setShowLinkInput, showPlayer]);

    const clearChart = useCallback(() => {
        if (showPlayer) {
            setShowLinkInput(false);
        }
    }, [showPlayer, setShowLinkInput]);

    const {locked, cheatCodeActivating} = useTokenLock(
        link,
        setLink,
        props.setUsedCheatCode,
        props.setSecondsRemaining
    );

   const {
        clear,
        onJack,
        loadJack,
        collectionAddress,
        artist,
        description,
        showPlayer,
        setShowPlayer,
        tokenId,
        searchTerm,
        songId,
        buyThisSkin,
        coverArt,
        songName,
        error,
        currentSong,
        setBuyThisSkin,
        jackedAudio,
        setCurrentSong,
        currentLink,
        metadata,
        setSearchTerm
    } = useJacker({
        remixToPlay,
        setLatestJacks,
        setShowHistory,
        setLink,
        setShowLinkInput,
        link,
        latestJacks,
        chillTokenId: props.isChillRX && props.match.params.tokenId,
        defaultSearch: props.defaultSearch,
        directTokenId: props.match.params.tokenId,
        directCollectionAddress: props.match.params.collectionAddress,
        playlistLoaded: props.playlistLoaded,

    });

    const {
        remixToPlay,
        setRemixToPlay,
        recordedRemix,
        uploadingRemix,
        onRemixSigningCompleted
    } =
          useRemixListener(clear, playerRef, props.match.params.remixId, playerRef);
 

    useEffect(() => {
        if (remixToPlay) {
            window.scrollTo(0,0);
        }
    }, [remixToPlay, setShowPlayer, setShowRemixes]);

    const {
        clearColors,
        coverGrad,
        buttonTextColor,
        buttonBgColor,
        upperGradInverted,
        lowerGradInverted
    } = useCoverArtColors({coverArt});

    let className = showPlayer ? "nft-jacker jacked" : "nft-jacker";
    if (showLinkInput) {
        className += " showing-link-input";
    }

    const onClickPassword = useCallback((e) => {
        if (password === "spores-next-gen") {
            setPassed(true);
        } else {
            setFailed(true);
        }
    }, [password, setFailed, setPassed]);


    if (!passed) {
        return (
            <div className="password-protected">
              <div className="spore-future">
              <img className="spore-future-logo" src="/spore-logo-future2.jpeg"/>
              <div className="password-box">
                <div className="password-label">
                  Password
                </div>
                <input type="password"
                  placeholder="Enter password here" text={password} onChange={(e) => setPassword(e.target.value)}/>
                {failed && <div className="password-failed">The password you entered is incorrect.</div>}
                <div onClick={onClickPassword} className="password-enter">
                  Enter
                </div>
              </div>
              </div>
            </div>
        );
    }
    return (
      <>
      {/* <SwipeableViews
        enableMouseEvents
        style={{overflow: isMobile ? "unset" : "hidden auto", backgroundColor: "black"}}
        slideStyle={{overflow: "hidden"}}
        index={swipeIndex}> */}

        <div
          ref={ref}
          onClick={() => setShowHistory(false)}
          className={className}
          style={{background: coverGrad}}>
          {!isUnlockedCollection(collectionAddress) && showPlayer && artist !== "Keyon Christ" && !recordedRemix && !uploadingRemix &&
           <TimeRemaining color={buttonBgColor}/>}
          {showPlayer && locked && artist !== "Keyon Christ" && !recordedRemix && !uploadingRemix &&
           !isUnlockedCollection(collectionAddress) &&
           <LockedNotification clearPlayer={() => {
              clear();
              clearColors();
         }}/>}
          {jackedAudio &&
           <SporeRecorded
             recordedRemix={recordedRemix}
             uploadingRemix={uploadingRemix}
             collectionAddress={collectionAddress}
             onRemixSigningCompleted={onRemixSigningCompleted}
             tokenId={tokenId}
             spTokenId={buyThisSkin}
             loadJack={loadJack}
           />}
          {showPlayer && !isMobile && <div
                                        onClick={() => {
                                            if (playerRef.current) {
                                                playerRef.current.contentWindow.postMessage({
                                                    type: "jacker-stop",
                                                    body: "jacker-stop"
                                                }, "*");
                                                playerRef.current.contentWindow.postMessage({
                                                    type: "clear-recording",
                                                    body: "clear-recording"
                                                }, "*");
                                            }
                                            onRemixSigningCompleted();
                                            setRemixToPlay(undefined);
                                            setShowPlayer(false);
                                            clear();
                                            clearColors();
                                        }}
                                        className="back-to-charts-btn">
                                        <IoMdArrowBack style={{fill: buttonBgColor}}/>
                                      </div>}
          {showHelp &&
           <PlayerHelper clear={() => setShowHelp(false)}/>
          }
          {showPlayer &&
           <div
             onClick={() => setShowHelp(true)} style={{color: lowerGradInverted}}
             className="helper-icon">
             ?
           </div>}
        <Navigation
          jacked={jackedAudio}
          setShowInfo={setShowInfo}/>
          {showPlayer && <div onClick={() => setShowHelp(true)} style={{color: lowerGradInverted}} className="helper-icon">?</div>}
          {artist === "Keyon Christ" &&
           <video
             playsInline
             className="rain-video"
             autoPlay
             loop
             muted
             src={"/mushrooms-medium.mp4"}/>}
          {error && <div className="error-message">{error}</div>}
          {showLinkInput && <Chart
            loadJack={loadJack} 
            searchTerm={searchTerm}
            jacked={jackedAudio}
            history={latestJacks}
            showChart={showLinkInput}/>}
          <div className={jackedAudio ? "player-container show" : "player-container hide"} onClick={() => setShowLinkInput(false)}>
            {remixToPlay && <RemixPlayer externalURL={currentLink} artist={artist} songName={songName} setRemix={setRemixToPlay} remix={remixToPlay}/>}
            <JackerPlayer
              showLoops={props.showLoops}
              playerRef={playerRef}
              collectionAddress={collectionAddress}
              songId={songId}
              remixToPlay={remixToPlay}
              locked={locked && !isUnlockedCollection(collectionAddress) && artist !== "Keyon Christ"}
              setBuyThisSkin={setBuyThisSkin}
              tokenId={artist === "Keyon Christ" || collectionAddress === CHILL_RX_COLLECTION ? tokenId : undefined}
              jackedAudio={jackedAudio}/>
          </div>
          {!remixToPlay && <ShuffleButton
                             buttonTextColor={buttonBgColor}
                             playerRef={playerRef}/>}
        
          {!isMobile && !remixToPlay && jackedAudio &&
           <SampleInfo
             playerRef={playerRef}
           />}
          <EchoSearchBox
            cheatCodeActivating={cheatCodeActivating}
            showLinkInput={showLinkInput}
            setShowHistory={setShowHistory}
            setShowInfo={setShowInfo}
            setLink={setLink}
            setSearchTerm={setSearchTerm}
            showHistory={showHistory}
            currentSong={currentSong}
            setCurrentSong={setCurrentSong}
            loadHistory={loadJack}
            latestJacks={latestJacks}
            onJack={onJack}
            loadJack={loadJack}
            link={link}
          />
         {songName &&
           <TokenMetadata
             buyThisSkin={buyThisSkin}
             showBuyThisSkin={collectionAddress !== CHILL_RX_COLLECTION}
             artist={artist}
             upperGradInverted={upperGradInverted}
             lowerGradInverted={lowerGradInverted}
             coverArt={coverArt}
             songName={songName}
             tokenId={tokenId}
             currentLink={currentLink}
           />}
          {showInfo && <Info clear={() => setShowInfo(false)}/>}
          {showPlayer && <div onClick={() => setShowLinkInput(!showLinkInput)}
          className="back-btn"
          style={
            isMobile ? 
            {backgroundImage: upperGradInverted == "#ffffff" ? "url('/search-icon.svg')" : "url('/search-icon-black.svg')"} : 
            {backgroundImage: buttonBgColor == "#ffffff" ? "url('/search-icon.svg')" : "url('/search-icon-black.svg')"}}/>}
        { showPlayer && <div
            className={"stockLeft"}
            onClick={() => isMobile && setShowRemixes(true)}
            style={{color: isMobile && showPlayer ? buttonTextColor : buttonBgColor, backgroundColor: isMobile ? buttonBgColor : "transparent"}}>
            <i>Remixes </i> {!isMobile && <HiOutlineChevronDoubleDown/>}
       </div>}
       { showPlayer && <ShareButton
                        url={"spores.vision/play/" + collectionAddress + "/" + tokenId}
                        type={"song"}
                        lowerGradInverted={lowerGradInverted}
                        buttonBgColor={buttonBgColor}/>}
      { showPlayer && showRemixes &&
      <RemixList
        remixToPlay={remixToPlay}
          setRemixToPlay={setRemixToPlay}
          collectionAddress={collectionAddress}
          tokenId={tokenId}
          clear={() => setShowRemixes(false)}/>}

        </div>

      {/* <div className='playlist-view' onClick={() => setSwipeIndex(0)}></div> */}
      {/* </SwipeableViews> */}
    </>);
};

const mapStateToProps = (state, props) => {
  return {
      playlistLoaded: state.playlist.playlistLoaded
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setUsedCheatCode: x => dispatch(setUsedCheatCode(x)),
      setSecondsRemaining: x => dispatch(setSecondsRemaining(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(NFTJacker);
