import React, { useEffect, useCallback, useState} from 'react';

export const useSporeListener = (ref) => {
    const [loaded, setLoaded] = useState(false);
    useEffect(() => {
        window.addEventListener("message", onReceive, false);
        return () => window.removeEventListener("message", onReceive);
    }, [setLoaded]);

    const onReceive = useCallback((e) => {
        /*
        if (e.origin !== "https://zequencer.mypinata.cloud" && e.origin !== "https://main.djdhx85izle40.amplifyapp.com/") {
            return;
        }
        */
        if (e.data === "Finished Loading Project") {
            // we are ready to send in the song via an iframe message
            setLoaded(true);
        }
  
    }, [setLoaded]);

    return {loaded};
};
