import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './pitch.scss';

const NUM_PAGES = 3;
const Gen3Info = () => { 
    let [showPitch, setShowPitch] = useState(false);
    let [page, setPage] = useState(1);
    if (showPitch) {
        return (
            <div className="gen3-info">
              <div onClick={() => setShowPitch(false)} className="pushable exit-btn">x</div>
              <img
                onClick={() => page < NUM_PAGES ? setPage(page+1) : 0}
                className="pitch-deck-image" src={`/spores-pitch-${page}.jpg`}/>
              <div className="page-navigator">
                <div className={page > 1 ? "prev-btn" : "prev-btn hide"} onClick={() => setPage(page-1)}>
                </div>
                <div className="page-info">
                  {page} / 3
                </div>
                <div className={page < NUM_PAGES ? "next-btn" : "hide next-btn"} onClick={() => setPage(page+1)}>
                </div>
              </div>
            </div>);
    } else {
        return (
            <div onClick={() => setShowPitch(true)} className="gen3-btn pushable">
              <div className="gen3-text">
                Gen3 Vision 
              </div>
            </div>
        );
    }
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Gen3Info);
