import BackendQuery from 'decentralized/BackendQuery.js';
import {ethers} from 'ethers';
import {SampleTagged, SampleCreated} from 'decentralized/EthereumEvents.js';
import {web3} from 'decentralized/web3.js';

const GUILD_SAMPLES = "0xc77d4e72dF7D0Bf96488eF543253af537fEb8737";

export const getSampleInfo = async (id) => {
    let query = new BackendQuery(GUILD_SAMPLES)
        .from(SampleCreated)
        .where("ipfsHash", id);

    let unfiltered = await query.executeQuery();

    console.log("unfilteeded=", unfiltered);

    let item = unfiltered[0];
    let tagsQuery = new BackendQuery(GUILD_SAMPLES)
        .from(SampleTagged)
        .where("ipfsHash", id);

    let tagResults = await tagsQuery.executeQuery();
    console.log("TAG RESULTS=", tagResults);
    return {
        ... item,
        ... tagResults[0]
    };
};
