import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import ipfs from 'decentralized/Ipfs.js';

let cache = {};
const HistoryItemSource = ({item}) => { 
    let [data, setData] = useState();
    useEffect(() => {
        if (cache[item.data]) {
            setData(cache[item.data]);
        }
        ipfs.getJson(item.data).then((data) => {
            cache[item.data] = data;
            setData(data);
        });
    }, [item]);

    if (!data) {
        return '';
    }

    return (<div className="history-item-source">
              {!item.title.includes(data.artist) && data.artist}
              {data.link && <Source link={data.link}/>}
            </div>);
};


export const Source = ({link, textColor}) => {
    let source = '';
    if (link.includes('zora')) {
        source = 'zora';
    } else if (link.includes("catalog.works")) {
        source = 'catalog.works';
    } else if (link.includes("mintsongs")) {
        source = 'mintsongs';
    } else if (link.includes("sound.xyz")) {
        source = 'sound.xyz';
    } else if (link.includes("opensea")) {
        source = 'OpenSea';
    }

    return (<div style={{color: textColor}} className="source">{source}</div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HistoryItemSource);
