import React, { memo, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

const HEIGHT = 100;
export default memo(({width, waveform, start, length, height=100}) => { 

    let [pts, setPts] = useState([]);

    useEffect(() => {
        let x = [];

        for (let i=0 ; i < waveform.length; i += 4) {
            x.push(waveform[i]*(height-.25)/HEIGHT);
        }
        setPts(x);
    }, [waveform, setPts]);

    let _start = start/100*pts.length;
    return (
        <svg width={width*2} height={height}>
          {pts.map((y, i) => <rect className={i >= _start && i <= _start + length/100*pts.length ? "sliced-region" : ""} fill={i%2==0 ? "white" : "transparent"} x={2*i/pts.length*width} y={height/2-y/2} height={y} width={2}/>)}
        </svg>
    );
});

