import React, { useEffect, useCallback, useState} from 'react';
import {usePlaylists} from 'components/hooks/usePlaylists.js';
import PlaylistInfo from './PlaylistInfo.js';
import {connect} from 'react-redux';

const JuicePlaylists = ({loadJack}) => { 
    let playlists = usePlaylists();
    return (
        <div
          onClick={e => e.stopPropagation()}
          className="playlist-container">
          <div
            className="playlist-item">
            {playlists.map(
                playlist =>
                <PlaylistInfo
                  loadJack={loadJack}
                  playlist={playlist}/>)}
          </div>
        </div>);
}

export default JuicePlaylists;
