import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import BuyUnsold from './BuyUnsold.js';
import {isMobile} from 'react-device-detect';

const BuyThisSkin = ({buyThisSkin, upperGradInverted, lowerGradInverted}) => { 
    if (buyThisSkin > 20) {
        return '';
    }
    return (
        <BuyUnsold
          upperGradInverted={isMobile ? lowerGradInverted : upperGradInverted}
          text={isMobile ? "Buy" : "Buy this spore"}
          tokenId={buyThisSkin}/>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(BuyThisSkin);
