import Tone from 'tone';
import ipfsClient from 'decentralized/Ipfs.js';
var bufferToArrayBuffer = require('buffer-to-arraybuffer');

class AudioUnit {
    constructor(context=Tone.Master.context) {
        this.duration = 0;
        this.source = undefined;
        this.buffer = undefined;
        this.playbackRate = 1;
        this.looping = false;
        this.playSound.bind(this);
        this.context = context;
        this.seek.bind(this);
        this.stopSound.bind(this);
        this.gain = new Tone.Gain(1);
        this.gain.connect(Tone.Master);
    }

    dispose() {
        if (this.source !== undefined) {
            this.source.disconnect();
        }
        this.buffer = undefined;
        this.source = undefined;
    }

    getContext() {
        return this.context;
    }

    playSound(x, duration) {
        if (x == undefined) {
            x = 0;
        }
        if (this.source != undefined) {
            this.source.stop();
        }
        this.source = Tone.Master.context.createBufferSource(); // creates a sound source
        this.source.connect(this.gain);       // connect the source to the context's destination (the speakers)
        this.source.buffer = this.buffer;
        this.source.playbackRate.value = this.playbackRate;
        this.source.loop = false;
        this.source.start(0, Math.max(0, x), duration);
    }

    seek(x) {
        this.stopSound();
        this.playSound(x);
    }

    stopSound() {
        if (this.source != undefined) {
            this.source.stop();
        }
    }

    loadRawBuffer(buffer) {
        this.buffer = buffer;
        this.duration = buffer.duration;
    }
    
    setBuffer(buffer) {
        this.buffer = buffer;
        this.duration = buffer.duration;
    }
}

export default AudioUnit;

