import React, { useEffect, useCallback, useState} from 'react';
import ZorbOption from './ZorbOption.js';
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import {connect} from 'react-redux';
import {useTokenOwners} from 'utils/zorbs.js';
import './zorb.scss';

const ZorbSelector = ({account, setZorbId, zorbId, setZorbStartPosition}) => { 
    let {zorbs} = useTokenOwners({account})

    useEffect(() => {
        if (zorbs[0]) {
            //setZorbId(parseInt(zorbs[0].tokenId));
        }
    }, [zorbs]);

    let [hide, setHide] = useState(false);
    useEffect(() => {
        if (zorbId !== undefined) {
            setTimeout(() => {
                setHide(true);
            }, 1000);
        }
    }, [zorbId]);
    if (hide) {
        return '';
    }
    return (<div className="zs-container">
            <div className="zorb-selector">
              {!zorbId && <p>
                            zorb + spore = zpores
                          </p>}
              {!zorbId && <p>
                            insert a zorb to power the spore and remix sound in its own way
                          </p>}
              {zorbs.length === 0 && account ? <div className="no-zorbs">No zorbs detected. </div> : zorbs.length === 0 &&
               <div className="connect-button-wrapper zorb-connect">
                 <ConnectButton
                   accountStatus="address" 
                   showBalance={{ smallScreen: false, largeScreen: false }}
                   label="Connect Wallet"
                 />
               </div>}

              <div className="zorbs-list">
                {zorbs.map(
                    x => <ZorbOption setZorbStartPosition={setZorbStartPosition} zorbId={zorbId} setZorbId={setZorbId} zorb={x}/>
                )}
              </div>
            </div>
            </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbSelector);
