import { createDecoder } from './minimp3-wasm.js';
import {sleep} from 'utils/sleep.js';
import Tone from 'tone';

let _counter=0;
let decoder;

let cache = {};

let mp3Recorder;
let results = {};

const decode = async (mp3Data, counter) => {
    if (!mp3Recorder) {
        mp3Recorder = new Worker(window.isNFT ? "mp3Worker.js" : "/mp3Worker.js");
        mp3Recorder.onmessage = (e) => {
            results[e.data.id] = e.data;
        };
    }
    mp3Recorder.postMessage({mp3Data, isNFT: window.isNFT, id: counter});

    while (!results[counter]) {
        await sleep(100);
    }
    return results[counter];
}

export const decodeMP3 = async (mp3Data) => {
    const {decoded, sampleRate, position} = await decode(mp3Data, _counter++);
    let ctxt = Tone.Master.context;
    let channels = 2;
    const _buffer = ctxt.createBuffer(channels, position/2, sampleRate); 
    const channelDataL = _buffer.getChannelData(0);
    const channelDataR = channels === 2 ? _buffer.getChannelData(1) : null;
    // int16 -> float32
    for (let i = 0; i < decoded.length; i+=2) {
      const l = decoded[i];
      const r = decoded[i+1];
      channelDataL[i/2] = l >= 32768 ? -(65536 - l) / 32768 : l / 32767;
        if (channels === 2) {
            channelDataR[i/2] = r >= 32768 ? -(65536 - r) / 32768 : r / 32767;
        }
    }
    return _buffer;
};

