import React, { useEffect, useCallback, useState} from 'react';
import {isMobile} from 'react-device-detect';
import History from './History.js';
import {connect} from 'react-redux';
import './search-box.scss';

const EchoSearchBox = ({
    showLinkInput,
    setSearchTerm,
    setShowHistory,
    onJack,
    loadJack,
    link,
    setShowInfo,
    setLink,
    showHistory,
    currentSong,
    setCurrentSong,
    loadHistory,
    latestJacks,
    cheatCodeActivating
}) => { 
    let className =!showLinkInput ? "nft-link hide" : "nft-link";
    if (cheatCodeActivating) {
        className += "  activating";
    }
  return (
      <div className={className}>
        <input
          onKeyDown={e => e.key === "Enter" ? onJack() : []}
          onClick={e => e.stopPropagation()}
          onFocus={() => {
              setShowHistory(true);
              setShowInfo(false);
          }}
          placeholder={isMobile ? "Search or drop a music NFT Link" : "Search an artist or drop a music NFT link"} value={link} onChange={e => {
              setShowHistory(false);
              setShowInfo(false);
              setLink(e.target.value);
          }}/>
        <div onClick={() => {
            setLink('');
            setSearchTerm('');
        }} className="clear-btn">x</div>
        <div onClick={(e) => {
            e.stopPropagation();
            onJack();
        }} className="pushable jack-btn">
          Remix
        </div>
        {cheatCodeActivating &&
         <div className="activation-text">
           Code activated. You may freely create bangers, for the culture.
         </div>}
      </div>);
};

export default EchoSearchBox;
