import React, {useState, useEffect, useCallback} from 'react';
export const useVideoSize = (videoRef) => {
    let [height, setHeight] = useState(50);
    let [width, setWidth] = useState(50);
    useEffect(() => {
        videoRef.current.addEventListener("loadeddata", onLoad);
        return () => videoRef.current.removeEventListener("loadeddata", onLoad);
    } , [setHeight, setWidth]);

    const onLoad = useCallback(() => {
        let height = videoRef.current.offsetHeight;
        setHeight(height);
        let width = videoRef.current.offsetWidth;
        setWidth(width);
    }, [setHeight, setWidth]);


    return {width, height}
};
