import HistoryItemSource from './HistoryItemSource.js';
import {shuffle} from 'utils/shuffle.js';
import {getTokensOnSale} from 'components/hooks/useTokensOnSale.js';
import {isMobile} from 'react-device-detect';
import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './history.scss';
import ipfs from 'decentralized/Ipfs.js';

let cachedTokens = undefined;
export const KEYON_SONGS = async () => {
    let tokensOnSale = cachedTokens || await getTokensOnSale();
    cachedTokens = tokensOnSale;

    let unsold = tokensOnSale.filter(x => x.askPrice);
    shuffle(unsold);
    let [a,b,c] = unsold;
    a = a ? a.tokenId : 12;
    b = b ? b.tokenId : 14;
    c = c ? c.tokenId : 18;
    return [
        {
            bpm: 90,
            title: "Keyon Christ - Psilocybin",
            isSporeTrack: true,
            metadata: {
                tokenId: a,
                artist: "Keyon Christ",
                name: "Psilocybin",
                url: "https://zequencer.mypinata.cloud/ipfs/QmeuwQQnjdqd51Uo1xkXLawYwYfXSq7T8h2mEWuJx7wHdx",
                link: "/sp/" + a,
                image: "/sp-thumbnail-" + a + ".png"
            }
        },
        {
            bpm: 106,
            title: "Keyon Christ - Twisting Faith",
            isSporeTrack: true,
            metadata: {
                tokenId: b,
                link: "/sp/" + b,
                artist: "Keyon Christ",
                name: "Twisting Faith",
                url: "https://zequencer.mypinata.cloud/ipfs/QmU6rmmjWWXCZd3NHjeu6pUVjBDZiMXuRx6EVtEocqz9dJ",
                image: "/sp-thumbnail-" + b + ".png"
            }
        },
        {
            bpm: 85,
            title: "Keyon Christ - Ying Yang Twins",
            isSporeTrack: true,
            metadata: {
                tokenId: c,
                link: "/sp/" + c,
                image: "/sp-thumbnail-" + c + ".png",
                artist: "Keyon Christ",
                name: "Ying Yang Twins",
                url: "https://zequencer.mypinata.cloud/ipfs/QmcD2Ece62eiv5FWegEyNquWN8WZzC3ZT81Wxhw6GRk4YY",
            }
        }
    ];
}

const History = ({
    currentSong,
    setCurrentSong,
    latestJacks,
    loadHistory,
    link
}) => { 
    let [keyonSongs, setKeyonSongs] = useState(undefined);

    useEffect(() => {
        KEYON_SONGS().then(x => setKeyonSongs(x));
    }, []);

    const load = useCallback((x) => {
        setCurrentSong(x);
        if (x.metadata) {
            loadHistory(x.metadata);
        } else {
            ipfs.getJson(x.data).then(
                y => loadHistory(y));
        }
    }, [loadHistory, setCurrentSong]);

    if (!keyonSongs) {
        return '';
    }
    let history = [... keyonSongs, ... latestJacks];

    if (link !== "") {
        history = history.filter(x => x.title && x.title.toLowerCase().includes(link.toLowerCase()));
    }

    return (<div className="jack-history">
              <img className="globe" src="/globe.svg"/>
              <div className="items">
                {history
                 .filter(x => x.title && x.title.trim() !== "") 
                 .map(
                  x => <div
                         onClick={() => load(x)}
                         className={(x.isSporeTrack ? "history-item spore-track" : "history-item") + (currentSong === x ? " current" : "")}>
                         {x.isSporeTrack ? x.title : trim(x.title)}
                         {x.isSporeTrack && <div className="source">{isMobile ? "by Spores" : "Released by Spores"}</div>}
                         {x.isSporeTrack ? (!isMobile ? <img src="/spores-icon-glow2.svg"/> : '') :
                          <HistoryItemSource item={x}/>}


                       </div>)}

              </div>
            </div>);
};

const trim = (x) => {
    let len = isMobile ? 24 : 35;
    if (x.length < len) {
        return x;
    }
    return x.slice(0, len) + '...';
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(History);
