import React, { useRef, useEffect, useCallback, useState} from 'react';
import ENSName from 'components/ens/ENSName.js';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import JollyRancherPlayer from './JollyRancherPlayer.js';
import {isMobile} from 'react-device-detect';
import SporeHeatMap from "components/spore/SporeHeatMap.js";

import {useInterval} from "components/hooks/useInterval.js";
import PlayButton from "components/buttons/PlayButton.js";
import {connect} from 'react-redux';
import Tone from 'tone';

const AudioPlayer = ({
    songName="",
    artist="",
    heat,
    externalURL,
    creator,
    audioUnit,
    store,
    spore,
    preset,
    setPreset,
    sporeMetadata
}) => { 

    let WIDTH=isMobile ? 230 : 500;
    let ref = useRef();
    let [playing, setPlaying] = useState(false);
    let [playTime, setPlayTime] = useState(undefined);
    let startPoint = useRef(0);
    let [progress, setProgress] = useState(0);
    let [mouseSeeker, setMouseSeeker] = useState();

    useEffect(() => {
        setPlaying(false);
        setProgress(0);
    }, [spore]);

    useEffect(() => {
        setPlaying(false);
        setProgress(0);
        setPlayTime(undefined);
        startPoint.current = 0 ;
    }, [spore, setPlaying, setProgress, setPlayTime]);

    const onTick = useCallback(() => {
        let diff = Tone.Master.context.currentTime - playTime;
        if (playing) {
            setProgress((diff + startPoint.current) / audioUnit.duration);
        }
    }, [playing, setProgress, playTime]);

    useInterval(onTick, 40);

    const onClickPlay = useCallback(() => {
        if (playing) {
            startPoint.current = startPoint.current + Tone.Master.context.currentTime - playTime;
            audioUnit.stopSound();
        } else {
            audioUnit.playSound(startPoint.current);
        }
        setPlaying(!playing);
        //setProgress(0);
        setPlayTime(Tone.Master.context.currentTime);
    }, [playing, setProgress, setPlaying, audioUnit, playTime]);

    const mouseSeek = useCallback((e) => {
        e.stopPropagation();
        setMouseSeeker(undefined);
        if (!playing) {
            setPlaying(true);
        }
        let x = e.clientX - ref.current.getBoundingClientRect().left;
        let percentage = x / WIDTH;
        let startTime = percentage * audioUnit.duration;
        audioUnit.playSound(startTime);
        startPoint.current = startTime;
        setPlayTime(Tone.Master.context.currentTime);
        setMouseSeeker(x);
        setTimeout(() => {
            setMouseSeeker(undefined);
        }, 300);
    }, [playing, setPlaying, setPlayTime, setMouseSeeker, audioUnit]);

    useEffect(() => {
        return () => {
            if (audioUnit) {
                audioUnit.stopSound();
            }
        }
    }, [audioUnit]);

    progress = Math.min(100, progress);

    return (
        <div className={audioUnit ? "audio-player" : "audio-player disabled"}>
          {audioUnit &&
           <PlayButton isPlaying={playing}
                       play={onClickPlay}/>}
          <div
            ref={ref}
            onClick={mouseSeek}
            className="play-time-and-slider">
            {!audioUnit && <div className="progress-container">
                           <UnknownProgressBar />
                         </div>}
            {sporeMetadata && <JollyRancherPlayer
                        setPreset={setPreset}
                                width={WIDTH}
                        preset={preset}
                        progress={progress}
                        store={store}
                        spore={sporeMetadata}
                      />}
           <div className="play-time">
              {audioUnit && <div className="time">
              {formatTime(progress*audioUnit.duration)} / {formatTime(audioUnit.duration)}
                            </div>}
              {creator && <div className="creator">
                            Remixed by <span className="wallet-address"><a target="_blank" href={"https://etherscan.io/address/"+creator}><ENSName address={creator}/></a></span>
                          </div>}
            </div>
            <div className="song-title-and-author">
              <div className="song-title">
                {songName}
              </div>
              <div className="song-creator">
                Original by <span className="creator-name"><a target="_blank" href={externalURL}>{artist}</a></span>
              </div>
            </div>
          </div>
        </div>);
}

export const truncate = (creator) => {
    return creator.slice(0, 4) + '...' + creator.slice(-4);
};

const formatTime = (time) => {
    if (!time) {
        time = 0;
    }
    let minutes = Math.floor(time/60);
    let seconds = Math.floor(time - minutes * 60);
    if (seconds < 10) {
        seconds = '0' + seconds;
    }

    return `${minutes}:${seconds}`;

}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

            /*
            <div
              onClick={mouseSeek}
              ref={ref} className="play-slider">
              {mouseSeeker && <div style={{left: mouseSeeker}} className="mouse-seeker"/>}
              <SporeHeatMap duration={audioUnit.duration} center={true} height={8} heat={heat} width={WIDTH}/>
            <div
              style={{width: WIDTH * progress}}
              className="play-slider-inner">
            </div>
            */
 

/*


*/
export default connect(mapStateToProps, mapDispatchToProps)(AudioPlayer);
