import React, { useEffect, useCallback, useState} from 'react';
import {isMobile} from 'react-device-detect';
import {useAudioPlayer} from 'components/hooks/useAudioPlayer.js';
import {connect} from 'react-redux';
import JollyRancherPlayer from '../spore/JollyRancherPlayer.js';
import './heatmap-player.scss';

const HeatmapPlayer = ({recordingHash, spore, disabled}) => { 
    let {audioPlayer, dispose} = useAudioPlayer(recordingHash);
    let className = "heatmap-player ";
    if (disabled) {
        className += " disabled";
    }
    return (
        <div className={className}>
          <JollyRancherPlayer width={isMobile ? 250 : 450} height={80} spore={spore}/>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HeatmapPlayer);
