import React, { useEffect, useCallback, useState} from 'react';
import PlayButton from '../buttons/PlayButton.js';
import {connect} from 'react-redux';

const SimpleControls = ({playerRef}) => { 
    let [playing, setPlaying] = useState(false);
    let [started, setStarted] = useState(false);
    useEffect(() => {
        window.addEventListener("message", onMessage);
    }, [setPlaying, setStarted]);

    const play = useCallback(() => {
        console.log('sending toggle play');
        playerRef.current.contentWindow.postMessage({
            type: "toggle-play",
            body: true
        }, "*");
    }, []);

    const onMessage = useCallback((e) => {
        if (e.data.type === "jacker-status") {
            let playing = parseInt(e.data.body);
            setPlaying(playing);
            setStarted(true);
        }
    }, [setPlaying, setStarted]);
    if (!started) {
        return;
    }
    return (<div className="simple-controls">
              <PlayButton play={play} isPlaying={playing}/>
            </div>
           );
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SimpleControls);
