
import Gen3Info from './Gen3Info.js';
import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import Drop from './Drop.js';
import './thugger.scss';
import './nextgen.scss';

//const CID = "QmWEEvyNZmr3kngUjE8TNRRC5FmnXZLC1TdQk87UYe1cQL";
const CID = "QmbtfXrV8ZUxM5PweH5EZKnpawpBDQsPHvn4Fk9rXzM3Vg";

const Thugger = (props) => { 
    let [entered, setEntered] = useState(false);
    let [remove, setRemove] = useState(false);

    useEffect(() => {
        if (entered) {
            setTimeout(() => {
                setRemove(true);
            }, 1000);
        }
    }, [entered, setRemove]);

    return (
        <div className="next-gen">
          {!remove && <div className={!entered ? "enter-overlay" : "enter-overlay hide"}>
                        <div
                          onClick={() => setEntered(true)}
                          className="inner-overlay">
                          <img
                            className=""
                            src="/spore-logo-future2.jpeg"/>
                          <div className="click-text">
                            Click to Enter
                          </div>
                        </div>
                      </div>}
          {remove && <Gen3Info/>}
        <Drop
          hideWaveform={true}
          color="black"
          url={`https://zequencer.mypinata.cloud/ipfs/${CID}/?tokenId=32&gallery=true`}
          collectionAddress="0x0000000000000000000000000000000000000001"
          isFullScreen={true}
          tokenId={1}
          className="thugger"
          artist="Young Thug"
          isSimple={true}
          songTitle="Hop Out Ft. Wiz Kalifa">
          {/*
         <p className="tagline-p">
           Spores grows strains of songs and media.
         </p>
         <p className="tagline-p">
           Season 1
         </p>
           */}
       </Drop>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Thugger);
