const CHAOS_COLLECTION = "0x8427e46826a520b1264b55f31fcb5ddfdc31e349";
const CHAOS_METADATA  = "https://opensea.mypinata.cloud/ipfs/bafybeic3pw5dd6tsu67mpgcm3mj5x335nfmq7xpesomv3uyuqluv2srsg4/";
const OPEN_SEA_PINATA = "https://opensea.mypinata.cloud/ipfs/";

export const getMetadata = (url) => {
    return new Promise(resolve => {
        if (url.startsWith("ipfs://")) {
            url = "https://zora-prod.mypinata.cloud/ipfs/" + url.slice(7);
        }
        if (url.includes("ipfs.io")) {
            url = url.replace("ipfs.io", "zora-prod.mypinata.cloud");
        }
        console.log("URL TO GET IS NOW=", url);
        fetch(url).then(
            r => r.json())
            .then(
                r => {
                    let {animation_url, audio_url} = r;
                    console.log("ANIMATION_URL=%s audio_url=%s", animation_url, audio_url);
                    let json = {
                        ... r
                    };
                    if (animation_url) {
                        if (animation_url.includes("ipfs")) {
                            json.ipfs = animation_url.slice(7);
                            json.url =OPEN_SEA_PINATA + animation_url.slice(7);
                        } else {
                            json.url = animation_url;
                        }
                    };
                    if (audio_url) {
                        if (audio_url.includes("/ipfs/")) {
                            let indexOf = audio_url.indexOf("/ipfs");
                            json.ipfs = audio_url.slice(indexOf+6);
                        }
                    }
                    resolve(json);
                });
    });
};

