const getParts = async (url) => {

    let part = "/ethereum/";
    if (url.toLowerCase().includes("chaos")) {
       var re = /#[1-9]\d*\b/g;
        var matches = url.match(re); 
        console.log("matches =", matches);
        if (matches[0]) {
            return {
                contract: CHAOS_COLLECTION,
                tokenId: parseInt(matches[0].slice(1))
            };
        } else {
            return {
                contract: CHAOS_COLLECTION,
                tokenId: 1
            };
        }
    } else if (url.includes("sound.xyz")) {
        console.log("SOUND XYZ TYPE");
        let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/soundxyz/" : "https://zequencer.io/soundxyz/";
        if (!window.LOCAL_BACKEND_FLAG) {
            baseURL = "https://zequencer.io/soundxyz/";
        }
        let indexOf = url.indexOf("sound.xyz");
        let soundxyzPath = url.slice(indexOf + 10);
        let ret = await fetch(baseURL + soundxyzPath);
        console.log("SOUND XYZ returned=", ret);
        let json = await ret.json();
        url = json.contractAddress + "/" + 1;
        let release = soundxyzPath.split("/")[1];
        return {contract: json.contractAddress, tokenId: "1", release};
    } else if (url.includes("catalog.works")) {
        console.log("CATALOG TYPE");
        console.log("url container catalog=", url);
        let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/catalog/" : "https://zequencer.io/catalog/";
        if (!window.LOCAL_BACKEND_FLAG) {
            baseURL = "https://zequencer.io/catalog/";
        }
        let str = "catalog.works/";
        let indexOf = url.indexOf(str);
        let soundxyzPath = url.slice(indexOf + str.length);
        console.log('sound xyz path =', baseURL + soundxyzPath);
        let ret = await fetch(baseURL + soundxyzPath);
        let json = await ret.json();
        return {contract: json.contractAddress}; //, tokenId: json.tokenId};
    } else if (url.includes("zora.co")) {
        part = "/collections/";
    } else if (url.includes("mintsongs.com")) {
        part = "/tokens/";
    }
    let indexOf = url.indexOf(part);
        url = url.slice(indexOf + part.length);
        let p = url.split("/");
    if (p[0] === "zora") {
        p[0] = "0xabEFBc9fD2F806065b4f3C237d4b59D9A97Bcac7";
    }
    return {
        contract: p[0],
        tokenId: p[1],
    };
};

const getZoraAPI = async (url, release="") => {
    if (url.includes("zora")) {
        let part = "/collections/";
        let indexOf = url.indexOf(part);
        url = url.slice(indexOf + part.length);
        let parts = url.split("/")
        if (parts[0] === "zora") {
            parts[0] = "0xabEFBc9fD2F806065b4f3C237d4b59D9A97Bcac7";
        }
        url = parts.join("/");
    } else if (url.includes("sound.xyz")) {
        //let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/soundxyz/" : "https://zequencer.io/soundxyz/";
        let baseURL = "https://zequencer.io/soundxyz/";
        let indexOf = url.indexOf("sound.xyz");
        let soundxyz = url.slice(indexOf + 10);
        let ret = await fetch(baseURL + soundxyz);
        let json = await ret.json();
        url = json.contractAddress + "/" + release;
    } else {
        let part = "/ethereum/";
        let indexOf = url.indexOf(part);
        url = url.slice(indexOf + part.length);
    }
    
    //let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/jacker/" : "https://zequencer.io/jacker/";
    let baseURL = "https://zequencer.io/jacker/";

//    if (window.location.hostname === "localhost" && window.HAS_LOCAL_BACKEND) {
     //   baseURL =  "/jacker/"; 
 //   } 
    try {
        let ret = await fetch(baseURL + url);
        return await ret.json();
    } catch(e) {
        return undefined;
    }
};
 
function parse_url(url) {
    var pattern = RegExp("^(([^:/?#]+):)?(//([^/?#]*))?([^?#]*)(\\?([^#]*))?(#(.*))?");
    var matches =  url.match(pattern);
    return {
        scheme: matches[2],
        authority: matches[4],
        path: matches[5],
        query: matches[7],
        fragment: matches[9]
    };
}

const isChaos = (url) => {
    return url.includes(CHAOS_COLLECTION);
};
