import ipfs from 'decentralized/Ipfs.js';
import {signMessage} from './sign.js';

import BackendQuery from 'decentralized/BackendQuery.js';
import {NewSporesPlaylist, SporesPlaylistSong } from 'decentralized/EthereumEvents.js';
import SporesPlaylistContract from 'decentralized/contracts/SporesPlaylistContract.js';

const playlistMessage = (cid) => "A new playlist awakens at " + cid;

export const newPlaylist = async (signer, collectionAddresses, tokenIds, name, description, thumbnail, songs) => {
    let metadata = {
        name,
        description,
        thumbnail,
        tokenIds,
        collectionAddresses,
        playlist: songs
    };
   
    let metadataCID = await ipfs.addJson(metadata);

    // genereate message based on the
    let message = playlistMessage(metadataCID);
    let {account, flatSignature, splitSignature} = await signMessage(signer, message);
    
    // now store this signed message on the ropsten contract
    let {r,s,v} = splitSignature;

    let body = {
        r,
        s,
        v,
        message,
        metadataCID,
        tokenIds,
        collectionAddresses,
    };
    console.log(body);
        
    let baseURL = "https://zequencer.io";
    /*
    if (window.location.hostname.includes("localhost")) {
        baseURL = "";
    }
    */

    let resp = await fetch(baseURL + '/newSporesPlaylist/' , {
        method: 'POST',
        body: JSON.stringify(body),
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        }
    });
    let respJson = await resp.json();
    
    return respJson.transactionHash;
};

const ADDRESS = new SporesPlaylistContract().contractAddress;
export const getJuicePlaylists = async () => {
    let query = new BackendQuery(ADDRESS)
        .from(NewSporesPlaylist);

    let results = await query.executeQuery();

    for (let result of results) {
        let {metadataCID} = result;
        result.metadata = await ipfs.getJson(metadataCID);
    }
    return results.slice(0, results.length - 3);
};
