import React, {useState, useEffect, useCallback} from 'react';
import {CHILL_RX_COLLECTION} from 'constants/collections.js';
import {getChillTokenId} from 'spores/chillRX.js';
import {getGatewayLink, extractIPFS} from 'utils/getGateway.js';
import {save} from 'jacker/save.js';
import {jack} from 'jacker/jack.js';
import {getArtistFromMetadata} from 'jacker/artist.js';
import {useTokenMetadata} from 'components/hooks/useTokenMetadata.js';
import {KEYON_SONGS} from 'components/nft-jacker/History.js';

export const useJacker = ({
    playlistLoaded,
    remixToPlay,
    setLatestJacks,
    setShowHistory,
    setLink,
    setShowLinkInput,
    link,
    latestJacks,
    chillTokenId,
    defaultSearch,
    directCollectionAddress,
    directTokenId,
    updateURL=true
}) => {
    const [currentLink, setCurrentLink] = useState();
    let [songId, setSongId] = useState();
    let [showDesc, setShowDesc] = useState(true);
    let [collectionAddress, setCollectionAddress] = useState();
    let [jackedAudio, setJackedAudio] = useState();
    let [buyThisSkin, setBuyThisSkin] = useState();
    let [tokenId, setTokenId] = useState();
    let [coverArt, setCoverArt] = useState();
    let [songName, setSongName] = useState();
    let [description, setDescription] = useState();
    let getTokenMetadata = useTokenMetadata();
    let [showPlayer, setShowPlayer] = useState();
    let [artist, setArtist] = useState();
    let [error, setError] = useState();
    let [currentSong, setCurrentSong] = useState();
    let [searchTerm, setSearchTerm] = useState(defaultSearch);

    useEffect(() => {
        if (!updateURL) {
            return;
        }
        if (remixToPlay) {
            window.history.pushState('page2', 'Title', '/play/' + collectionAddress + '/' + tokenId + '/' + remixToPlay.sporeId);
        } else if (collectionAddress) {
            window.history.pushState('page2', 'Title', '/play/' + collectionAddress + '/' + tokenId);
        } else {
            window.history.pushState('page2', 'Title', '/');
        }
    }, [remixToPlay, collectionAddress, tokenId, updateURL]);

    useEffect(() => {
        if (directCollectionAddress) {
            let link = "https://zora.co/collections/" + directCollectionAddress + "/" + directTokenId;
            jack(link, getTokenMetadata).then(
                jacked => {
                    loadJack(jacked);
                });
        }
    }, []);

    useEffect(() => {
        if (chillTokenId !== undefined) {
            let ret = getChillTokenId(chillTokenId);
            loadJack(ret);
        }
    }, [
      ]);

    useEffect(() => {
        KEYON_SONGS();
    }, []);

    const clear = useCallback(() => {
        setLink("");
        setCollectionAddress(undefined);
        setShowPlayer(false);
        setCoverArt(undefined);
        setArtist(undefined);
        setTokenId(undefined);
        setSongName(undefined);
        setDescription(undefined);
        setJackedAudio(undefined);
        setShowLinkInput(true);
        setSongId(undefined);
    }, [
        setSongId,
        setShowLinkInput,
        setShowPlayer,
        coverArt,
        setLink,
        setTokenId,
        setArtist,
        setSongName,
        setCollectionAddress,
        setDescription,
        setJackedAudio
    ]);

    const onJack = useCallback(async (isPlaylist) => {
        if (link === "") {
            return;
        }
        if (!link.startsWith("http")) {
            setSearchTerm(link);
            return;
        }
        setShowPlayer(true);
        if (isPlaylist) {
            setShowLinkInput(false);
        }
        setShowDesc(false);
        let ret = await jack(link, getTokenMetadata);
        if (!ret) {
            clear();
            setError("The token you\'ve linked has a strange format. Echo rejects it, respectfully...");
            return;
        }
        ret.link = link;
        loadJack(ret, true);
    }, [
        link,
        setSearchTerm,
        latestJacks,
        setSongName,
        setShowHistory,
        setCollectionAddress,
        playlistLoaded,
        setJackedAudio,
        setArtist,
        setTokenId,
        setShowPlayer,
        setError,
        setShowLinkInput,
        setSongId,
        setCurrentLink
    ]);

    const loadChillRX = useCallback((ret) => {
    
        setArtist("Chill RX");
        setTokenId(ret.tokenId);
        setSongId(ret.songId);
        setCollectionAddress(ret.collectionAddress);
        setSongName(ret.name);
        setCoverArt(ret.coverArt);
        setDescription(ret.description);
        setJackedAudio("BLANK");

    }, [setSongId, setCoverArt, setArtist, setArtist, setCoverArt, setCollectionAddress]);

    const loadJack = useCallback((ret, shouldSave=true, isPlaylist) => {
        setShowHistory(false);
        setShowPlayer(true);
        if (!isPlaylist) {
            setShowLinkInput(false);
        }
        setShowDesc(false);

        if (ret.collectionAddress === CHILL_RX_COLLECTION) {
            loadChillRX(ret);
            return;
        }
        
        let {url, ipfs} = ret;
       
        console.log("LOAD JACK CALLED ret=", ret);

        if (!ret.external_url) {
            ret.external_url = ret.link;
        }
        if (!ret.link && ret.external_url) {
            ret.link = ret.external_url;
        }
        if (!url) {
            clear();
            setError("The token you\'ve linked might not contain a music NFT.");
            return;
        }

        setError(undefined);

        let toSave = {};
        if (url.startsWith("ipfs://")) {
            ipfs = url.slice(7);
        }
        if (ipfs && !url) {
            let url = getGatewayLink(ret.external_url, ipfs);
            //submitSplitJob(url);
            setJackedAudio(ipfs);
            toSave.ipfs = ipfs;
            toSave.url = url;
            console.log("URL we extracted=", url);
        } else {
            let ipfsHash = extractIPFS(url);
            console.log("EXTRACTED IPFS=", ipfsHash);
            url = getGatewayLink(ret.external_url, ipfsHash);
            console.log('setting jacked audio=', url);
            setJackedAudio(url);
            toSave.url = url;
        }
        console.log("URL WE EXTRACTED = ", url);
        if (ret.body) {
            ret = {
                ... ret,
                ...ret.body,
                name: ret.body.title
            };
            if (ret.body.artwork && ret.body.artwork.info) {
                ret = {
                    ... ret,
                    image: ret.body.artwork.info.uri
                };
            }
        }
        let artist = getArtistFromMetadata(ret);
        toSave.artist = artist;
        setArtist(artist);

        console.log("TOKEN ID =", ret.tokenId);
        setTokenId(ret.tokenId);
        setSongId(ret.songId);
        setCollectionAddress(ret.collectionAddress);

        /*
        setSongId(2);
        setTokenId(2000);
        setCollectionAddress(CHILL_RX_COLLECTION);
        */

        setSongName(ret.name);
        toSave.song = ret.name;
        setDescription(ret.description);

        let image = ret.image;
        if (ret.image) {
            let image = getGatewayLink(ret.link, extractIPFS(ret.image));
            setCoverArt(image);
        } else {
            setCoverArt("/zora-placeholder.png");
        }

        toSave = {
            ... ret,
            ... toSave
        };

        if (ret.link) {
            setCurrentLink(ret.link);
        } else {
            setCurrentLink(undefined);
        }

        if (shouldSave && artist !== "Keyon Christ") {
            save(toSave);
            /*
            setLatestJacks([
                toSave,
                ... latestJacks,
            ]);
            */
        }
    }, [
        setCurrentLink,
        link,
        setLatestJacks,
        setTokenId,
        latestJacks,
        setSongName,
        setJackedAudio,
        setArtist,
        setShowPlayer,
        setError,
        setShowLinkInput,
        setShowHistory,
        setSongId,
    ]);

    return {
        clear,
        onJack,
        loadJack,
        artist,
        description,
        showPlayer,
        setShowPlayer,
        tokenId,
        buyThisSkin,
        coverArt,
        songName,
        error,
        currentSong,
        jackedAudio,
        setBuyThisSkin,
        currentLink,
        setCurrentSong,
        collectionAddress,
        songId,
        searchTerm,
        setSearchTerm,
        setArtist
    };
};

