import {CHILL_RX_COLLECTION} from 'constants/collections.js';

// as songs come in edit this list w/ their name directly
const SONG_NAMES = [
    "Chill Song 1",
    "Chill Song 2",
    "Chill Song 3",
    "Chill Song 4",
    "Chill Song 5",
    "Chill Song 6",
    "Chill Song 7",
    "Chill Song 8",
    "Chill Song 9",
    "Chill Song 10",
];

const SONG_TO_TOKENS = {
    1: [1,2,3],
    2: [4,5,6],
    3: [7,8,9],
    4: [10,11,12],
    5: [13,14,15],
    6: [16,17,18],
    7: [19,20,21],
    8: [22,23,24],
    9: [25,26,27],
    10: [28,29,30],
};

export const getChillRXSongs = () => {
    let songs = [];
    for (let i=1; i <= SONG_NAMES.length; i++) {
        songs.push({
            songName: SONG_NAMES[i-1],
            name: SONG_NAMES[i-1],
            collectionAddress: CHILL_RX_COLLECTION,
            coverArt: "/crx-" + i + ".png",
            tokenId: 1000, // todo: this will be randomly chosen of the actual tokenIds for htis song
            songId: i,
            tokenIds: SONG_TO_TOKENS[i]
        });
    }
    return songs;
};

export const getChillTokenId = (tokenId) => {
    console.log(getChillRXSongs);
    let song = getChillRXSongs().find(x => x.tokenIds.includes(parseInt(tokenId)));
    console.log('song we found');
    song = {
        ... song,
        tokenId
    };
    return song;
}
