
import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './timer.scss';

const TimeRemaining = ({secondsRemaining, color}) => { 
    secondsRemaining = Math.floor(secondsRemaining);
    if (secondsRemaining > 20 || isNaN(secondsRemaining)) {
        return '';
    }
    if (secondsRemaining < 10) {
        secondsRemaining = '0'+secondsRemaining;
    }
    let className = secondsRemaining%2 === 1 ? "time-remaining fade" : "time-remaining";
    return (
        <div className={className} style={{color}}>
          0:{secondsRemaining}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
      secondsRemaining: state.spore.secondsRemaining
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TimeRemaining);
