import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import SongWaveform from 'components/waveform/SongWaveform.js';
import {useSporesMessageHandler} from 'components/hooks/useSporesMessageHandler.js';
import {getSampleInfo} from 'spores/samples.js';
import './sample.scss';

const SampleInfo = ({playerRef}) => { 
    let [currentSample, setCurrentSample] = useState();
    let [sampleInfo, setSampleInfo] = useState();
    let [sampleWaveform, setSampleWaveform] = useState();

    useSporesMessageHandler("sample-changed", setCurrentSample);
    useSporesMessageHandler("sample-waveform", setSampleWaveform);

    useEffect(() => {
        if (currentSample) {
            getSampleInfo(currentSample[0]).then(setSampleInfo);
        }
    }, [currentSample]);

    const goToPrevious = useCallback(() => {
        playerRef.current.contentWindow.postMessage({type: "previous-shuffle-sample", body: "yo"}, "*");
    }, []);

    const goToNext = useCallback(() => {
        playerRef.current.contentWindow.postMessage({type: "next-shuffle-sample", body: "yo"}, "*");
    }, []);

    return (
        <div className="sample-info">
          <div className="name-and-arrows">
            <div className="sample-name">{sampleInfo && sampleInfo.title}</div>
            <div className="arrows">
              <div
                onClick={goToPrevious}
                className="arrow-btn pushable"/>
              <div
                onClick={goToNext}
                className="arrow-btn flip pushable"/>
            </div>
          </div>
          {sampleWaveform && <SongWaveform width={200} waveform={sampleWaveform}/>}
        </div>);
};

export default SampleInfo;
