import React, {useState, useEffect} from 'react';
import { ZDK, ZDKNetwork, ZDKChain } from "@zoralabs/zdk";

const networkInfo = {
    network: ZDKNetwork.Ethereum,
    chain: ZDKChain.Mainnet,
};

const API_ENDPOINT = "https://api.zora.co/graphql";
const args = { 
    endPoint:API_ENDPOINT, 
    networks:[networkInfo], 
};

const zdk = new ZDK(args) // All arguments are optional

export const useTokenOwners = ({account}) => {
    let [isLoading, setIsLoading] = useState(true);
    let [zorbs, setZorbs] = useState([]);

    useEffect(() => {
        if (account && account.address) {
            // use the ultra futuristic zora api to get see if they own
            // one of these open editions
            setIsLoading(true);
            console.log('loading collection info for account=', account.address);
            zdk.tokens({
                where: {
                    collectionAddresses: ["0xCa21d4228cDCc68D4e23807E5e370C07577Dd152"],
                    ownerAddresses: [account.address]
                }})
                .then(
                    results => {
                        setZorbs(results.tokens.nodes.map(x => x.token));
                    });
        }
    }, [account, setIsLoading, setZorbs]);

    return {zorbs, isLoading};
};
