import React, {useEffect, useState} from 'react';

export const useLatestJacks = () => {
    const [latestJacks, setLatestJacks] = useState();

    useEffect(() => {
        let baseURL = window.location.hostname === "localhost" ?
            "http://localhost:9090/latestJacks/" : "https://zequencer.io/latestJacks/";
        if (!window.LOCAL_BACKEND_FLAG) {
            baseURL = "https://zequencer.io/latestJacks/";
        }
        fetch(baseURL).then(
            async ret => {
                let json = await ret.json();
                setLatestJacks(json.filter(x => x.title && !x.title.includes("Spore Players")));
            });
    }, []);

    return {latestJacks, setLatestJacks};
};
