import Collapsible from 'react-collapsible';
import {isMobile} from 'react-device-detect';
import {
    setPlaylistLoaded,
    setCurrentPlaylistSong 
} from 'actions/index.js';
import {jack} from 'jacker/jack.js';
import {getJackFromToken} from 'utils/getJackFromToken.js';
import {useTokenMetadata} from 'components/hooks/useTokenMetadata.js';
import React, {useCallback} from 'react';
import './playlist.scss';
import {connect} from 'react-redux';

const PlaylistInfo = ({setCurrentPlaylistSong, setPlaylistLoaded, playlist, loadJack, currentSong, isLoaded}) => {
    let thumbnail = "https://zequencer.io/ipfs/" + playlist.metadata.thumbnail;
    let getTokenMetadata = useTokenMetadata();

    const onClick = useCallback((song) => {
        setCurrentPlaylistSong(song);
        setPlaylistLoaded(playlist);
        getJackFromToken(song.collectionAddress, song.tokenId).then(
            jacked => loadJack(jacked, undefined, !isMobile));
    }, [playlist]);

    return (<>
        <Collapsible
          transitionTime={200}
          easing="ease-in-out"
          open={isLoaded}
          trigger={
              <div className='playlist-trigger'>
                <img src={thumbnail} className="playlist-trigger-img"></img>
                <div className="playlist-name">{playlist.metadata.name}</div>
              </div>
          }>
          <p className='playlist-description'>{playlist.metadata.description}</p>
          <div className='playlist-list-container'>
            {playlist.metadata.playlist.map(
                (item, i) =>
                <div
                  onClick={() => onClick(item)}
                  className={
                      currentSong &&
                          currentSong.collectionAddress === item.collectionAddress &&
                          currentSong.tokenId === item.tokenId ?
                          "playlist-item current" : "playlist-item"}>
                  <div className="song-number">{i+1}</div>
                  <img src={item.coverArt} className="playlist-item-img"></img>
                  <p>{item.songName}</p>
                  <p>{item.artist}</p>
                </div>)}
          </div>
        </Collapsible>
    </>);
}


const mapStateToProps = (state, props) => {
  return {
      isLoaded: state.playlist.playlistLoaded && state.playlist.playlistLoaded.playlistId ===
          props.playlist.playlistId,
      currentSong: state.playlist.currentSong
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setPlaylistLoaded: x => dispatch(setPlaylistLoaded(x)),
      setCurrentPlaylistSong: x => dispatch(setCurrentPlaylistSong(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PlaylistInfo);
