import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {isMobile} from 'react-device-detect';

const VisualLinks = ({helperBox, helperRefs, selected}) => { 
    const [locations, setLocations] = useState();
    useEffect(() => {
        if (!helperRefs) {
            return;
        }
        let playerEl = helperRefs[selected].current;
        let helperEl = helperBox.current;

        let playerLocation = getLocation(playerEl);
        let helperLocation  = getLocation(helperEl);

        setLocations([helperLocation, playerLocation]);
    }, [selected, setLocations, helperRefs]);
    if (!locations) {
        return '';
    }

    let points = [
        isMobile ? locations[0].x : locations[0].x + locations[0].width,
        isMobile ? locations[0].y + locations[0].height : locations[0].y,
        isMobile ? locations[1].x + locations[1].width / 2 : locations[1].x,
        locations[1].y + locations[1].height/2-1,
        isMobile ? locations[1].x + locations[1].width / 2 : locations[1].x,
        locations[1].y + locations[1].height/2+1,
        locations[0].x + locations[0].width,
        locations[0].y+locations[0].height
    ];
    return (
        <svg className="visual-links-spore">
          <linearGradient id="AnnotationGradient">
            <stop offset="5%" stopColor="#a4c3e55c" />
            <stop offset="95%" stopColor="#80aaff36" />
          </linearGradient>
          <polygon className="visual-link" points={points}/>
        </svg>);
}

const getLocation = (element, bounding) => {
    var rect = element.getBoundingClientRect();
    let bound = {top: 0, left: 0} //bounding.getBoundingClientRect();
    let y = rect.top - bound.top;
    let x = rect.left - bound.left;
    let height = element.offsetHeight;
    let width = element.offsetWidth;
    let pos = {x, y, width, height};
    return pos;
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(VisualLinks);
